import React, { useState } from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import { Doughnut } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import logo from '../images/logo.png';
import Escala from './Escala';

function Unidades() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';

  const changeState = useDispatch();
  const viewUnidades = useSelector((state) => state.unidades);
  const [cleanrender, setcleanrender] = useState(0);

  // recuperando estados atualizados.
  const idusuario = useSelector((state) => state.idusuario);
  const usuario = useSelector((state) => state.nomeusuario);
  const funcao = useSelector((state) => state.funcao);
  const tipo = useSelector((state) => state.tipo);
  const hospital = useSelector((state) => state.nomehospital);

  // carregamento da lista de unidades de atendimento do hospital, nas quais o usuário trabalha.
  const [unidades, setUnidades] = useState([]);
  const loadUnidades = () => {
    // ROTA: SELECT * FROM usuarioxhospitalxunidade WHERE idusuario = idusuario AND hospital = hospital.
    axios.get(html + "/unidades/'" + idusuario + "'/'" + hospital + "'").then((response) => {
      setUnidades(response.data);
    });
  }
  // montando a lista de unidades.
  function ShowUnidades() {
    return (
      <div
        style={{
          display: 'flex',
          width: '100%',
          paddingLeft: 5,
          paddingRight: 5,
        }}>
        <div
          className="scroll"
          id="LISTA DE UNIDADES"
          style={{
            display: 'flex',
            width: '100%',
            height: '70vh',
            flexDirection: 'row',
            flexWrap: 'wrap',
            justifyContent: 'center',
            alignItems: 'center',
            borderRadius: 5,
            margin: 5,
          }}
        >
          {unidades.map((item) => (
            GetData(item)
          ))}
        </div>
      </div>
    );
  }

  // selecionando um hosital da lista.
  const selectUnidade = (item) => {
    if (tipo !== 3) {
      changeState({
        type: 'PACIENTES_ON',
        // payloads.
        payloadIdusuario: idusuario,
        payloadNomeusuario: usuario,
        payloadFuncao: funcao,
        payloadTipo: tipo,
        payloadNomehospital: item.hospital,
        payloadNomeunidade: item.unidade,
      });
    }
  }

  useEffect(() => {
    if (viewUnidades === 1) {
      setcleanrender(0);
      // carregando a lista de unidades.
      loadUnidades()
      // renderizando a tela após a construção "tempestuosa" dos gráficos.
      setTimeout(() => {
        setcleanrender(1);
      }, 1000);
    }
  }, [viewUnidades]);

  const selectEscala = (item) => {
    changeState({
      type: 'ESCALA_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
      payloadNomehospital: item.hospital,
      payloadNomeunidade: item.unidade,
    });
  }

  // CHART.
  /* gráfico em torta que exibe o total de leitos vagos e o total
  de leitos ocupados para cada unidade. */
  var pacientes = 0;
  var leitos = 0;
  var dataChart = [];
  function GetData(item) {
    const [pct, setpct] = useState(0);
    const [lto, setlto] = useState(0);
    // obtendo o total de pacientes internados na unidade.
    // ROTA: SELECT * FROM atendimentos WHERE hospital = item.hospital AND unidade = item.unidade.
    axios.get(html + "/pacientesunidade/'" + item.hospital + "'/'" + item.unidade + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      pacientes = x.length;
      setpct(pacientes);
    });
    // obtendo o total de leitos para a unidade.
    // ROTA: SELECT * FROM hospitaisxunidades WHERE hospital = item.hospital AND unidade = item.unidade.
    axios.get(html + "/totalleitosunidade/'" + item.hospital + "'/'" + item.unidade + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      const arrayleitos = x.map((item) => item.leitos);
      // efetuando a soma de leitos.
      function somaLeitos(total, num) {
        return total + num;
      }
      leitos = arrayleitos.reduce(somaLeitos);
      setlto(leitos);
    });
    // gerando os dados do gráfico.
    console.log('LEITOS: ' + lto + ', PACIENTES: ' + pct + '.');
    dataChart = {
      labels: [' LIVRES', ' OCUPADOS'],
      datasets: [
        {
          data: [lto - pct, pct],
          backgroundColor: [
            '#52be80',
            '#ec7063',
          ],
          borderColor: '#E1E5F2',
          hoverBorderColor: ['#E1E5F2', '#E1E5F2'],
        },
      ],
    };
    return (
      <div>
        <div
          className="card"
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            borderRadius: 5,
            padding: 10,
            margin: 5,
          }}
        >
          <p className="title2"
            style={{
              fontSize: 22,
              fontWeight: 'bold',
              margin: 10,
              padding: 0,
            }}>
            {item.unidade}
          </p>
          <div
            onClick={() => selectUnidade(item)}>
            <Doughnut
              data={dataChart}
              width={200}
              height={200}
              plugins={ChartDataLabels}
              options={{
                plugins: {
                  datalabels: {
                    display: function (context) {
                      return context.dataset.data[context.dataIndex] !== 0;
                    },
                    color: '#FFFFFF',
                    font: {
                      weight: 'bold',
                      size: 16,
                    },
                  },
                },
                tooltips: {
                  enabled: false,
                },
                hover: { mode: null },
                elements: {
                  arc: {
                    hoverBorderColor: '#E1E5F2',
                    borderColor: '#E1E5F2',
                    borderWidth: 5,
                  },
                },
                animation: {
                  duration: 0,
                },
                title: {
                  display: false,
                  text: 'OCUPAÇÃO DE LEITOS',
                },
                legend: {
                  display: false,
                  position: 'bottom',
                },
                maintainAspectRatio: true,
                responsive: false,
              }}
            />
          </div>
          <div style={{ flexDirection: 'column', justifyContent: 'center' }}>
            <div>
              <p
                className="title2"
                style={{
                  fontSize: 16,
                  fontWeight: 'bold',
                  margin: 2.5,
                  marginTop: 10,
                  padding: 0,
                }}
              >
                {'OCUPAÇÃO: ' + Math.ceil(pct * 100 / lto) + '%'}
              </p>
            </div>
            <div
              className="secondary"
              style={{
                flexDirection: 'row',
                marginTop: 5,
                marginBottom: 20,
              }}
            >
              <div
                id="VAGOS"
                style={{
                  width: 20,
                  height: 20,
                  borderRadius: 5,
                  backgroundColor: '#52be80',
                  margin: 2.5,
                  padding: 0,
                }}
              ></div>
              <p
                className="title2"
                style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
              >
                VAGOS
          </p>
              <div
                id="OCUPADOS"
                style={{
                  width: 20,
                  height: 20,
                  borderRadius: 5,
                  backgroundColor: '#ec7063',
                  margin: 2.5,
                  padding: 0,
                }}
              ></div>
              <p
                className="title2"
                style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
              >
                OCUPADOS
          </p>
            </div>
            {
              Escala(item)
            }
          </div>
        </div>
      </div>
    );
  }

  function Escala(item) {
    if (tipo === 2 || tipo === 3) { // tipos 2 (gestor) e 3 (secretária).
      return (
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', width: '100%' }}>
          <button className="blue-button" style={{ width: 150, alignSelf: 'center' }} onClick={() => selectEscala(item)}>
            {'ESCALA'}
          </button>
        </div>
      )
    } else {
      return null;
    }
  }

  // HEADER.
  const clickLogin = () => {
    changeState({ type: 'LOGIN_ON' });
  };
  // função para retornar à tela Login.
  const clickVoltar = () => {
    changeState({
      type: 'HOSPITAL_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
    });
  };
  // função para acessar a tela Password.
  const clickPassword = () => {
    changeState({
      type: 'PASSWORD_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
    });
  };
  // function Header.
  function Header() {
    return (
      <div
        id="HEADER"
        style={{
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'center',
          alignItems: 'center',
          margin: 5,
          padding: 5,
          width: '100%',
          maxHeight: '20vh',
        }}
      >
        <div
          className="secondary"
          style={{
            display: 'flex',
            flex: 1,
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <img
            alt=""
            src={logo}
            style={{
              marginTop: 15,
              marginBottom: 15,
              marginLeft: -5,
              marginRight: -5,
              height: '90%',
            }}
          ></img>
        </div>
        <div
          className="secondary"
          style={{
            display: 'flex',
            flex: 7,
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <div style={{ display: 'flex', flexDirection: 'row' }}>
            <p className="title1" style={{ color: '#1F7A8C' }}>
              {'LISTA DE UNIDADES: '}
            </p>
            <p className="title1" style={{ color: '#ec7063' }}>
              {hospital}
            </p>
          </div>
        </div>
        <div
          className="secondary"
          style={{
            display: 'flex',
            flexDirection: 'column',
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <button
            className="blue-button"
            title="CLIQUE PARA ALTERAR SUA SENHA."
            onClick={() => clickPassword()}
            style={{
              display: 'flex',
              width: 200,
              margin: 10,
              marginBottom: 0,
              paddingLeft: 20,
              paddingRight: 20,
            }}
          >
            OLÁ, {usuario}.
          </button>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
            <button
              className="blue-button"
              title="VOLTAR PARA A LISTA DE HOSPTAIS."
              onClick={() => clickVoltar()}
              style={{
                display: 'flex',
                width: 97.5,
                margin: 10,
                marginTop: 5,
                marginRight: 0,
                paddingLeft: 20,
                paddingRight: 20,
              }}
            >
              VOLTAR
            </button>
            <button
              className="blue-button"
              title="FAZER LOGOFF."
              onClick={() => clickLogin()}
              style={{
                display: 'flex',
                width: 97.5,
                margin: 10,
                marginTop: 5,
                marginLeft: 5,
                paddingLeft: 20,
                paddingRight: 20,
              }}
            >
              SAIR
            </button>
          </div>
        </div>
      </div>
    );
  }

  // renderização do componente.
  if (viewUnidades === 1 && cleanrender === 1) {
    return (
      <div
        className="main fade-in"
        style={{
          display: 'flex',
          width: '100%',
          flexDirection: 'column',
        }}
      >
        <Header></Header>
        <ShowUnidades></ShowUnidades>
      </div>
    );
  } else {
    return null;
  }
}
export default Unidades;
