import React from 'react';
import axios from 'axios';
import swal from 'sweetalert';
import { useSelector, useDispatch } from 'react-redux';

function Password() {
  const changeState = useDispatch();

  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';
  const viewPassword = useSelector((state) => state.password);

  const loginLogin = useSelector((state) => state.userlogin);
  const loginUsuario = useSelector((state) => state.username);
  const loginHospital = useSelector((state) => state.hospital);
  const loginCti = useSelector((state) => state.cti);
  const gestor = useSelector((state) => state.gestor);
  
  var senha = '';

  const updateSenha = () => {
    senha = document.getElementById('inputSenha').value;
  };

  const sendData = () => {
    if (senha === '') {
      swal({
        title: 'ERRO',
        text: 'Por favor, insira uma senha!',
        icon: 'warning',
        dangerMode: true,
      });
    } else {
      var obj = {
        user: loginLogin,
        password: senha.toUpperCase(),
      };
      axios.post(html + "/updatepassword/'" + loginLogin + "'", obj);
      swal('SUCESSO', 'Senha alterada com sucesso!', 'success');
      changeState({
        type: 'CTI_ON',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadHospital: loginHospital,
        payloadCti: loginCti,
        payloadGestor: gestor,
      });
    }
  };

  // eslint-disable-next-line
  if (viewPassword === 1) {
    return (
      <div>
        <div
          className="secondary"
          style={{
            height: '70vh',
          }}
        >
          <label
            className="title2"
            style={{
              marginTop: 20,
              marginBottom: 10,
            }}
          >
            ALTERAR SENHA
          </label>
          <label
            style={{
              fontWeight: 'bold',
              fontSize: 24,
              color: 'red',
              textAlign: 'center',
              marginTop: 10,
              marginBottom: 10,
            }}
          >
            USUÁRIO: {loginUsuario}
          </label>
          <label style={{ marginTop: 20, marginBottom: 10, fontSize: 16 }}>
            POR FAVOR, INSIRA A NOVA SENHA:
          </label>
          <input
            className="input"
            onChange={() => updateSenha()}
            autoComplete="off"
            style={{
              width: 200,
              height: 50,
            }}
            placeholder="NOVA SENHA"
            onFocus={(e) => (e.target.placeholder = '')}
            onBlur={(e) => (e.target.placeholder = 'NOVA SENHA')}
            type="password"
            id="inputSenha"
          ></input>
          <h3
            style={{
              marginTop: 10,
              marginBottom: 20,
              fontSize: 12,
              fontWeight: 'bold',
            }}
          >
            PROCURE USAR PELO MENOS 6 CARACTERES (SÃO ACEITAS APENAS LETRAS
            MAIÚSCULAS).
          </h3>
          <button
            className="green-button"
            onClick={() => sendData()}
            style={{
              marginTop: 20,
              marginBottom: 5,
              width: 100,
            }}
          >
            OK
          </button>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Password;
