import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import deletar from '../images/deletar.svg';
import salvar from '../images/salvar.svg';
import Toast from './Toast';

function Evolucao(
  { viewevolucao,
    idevolucao,
    data,
    idatendimento,
    idusuario,
    usuario,
    funcao,
    idusuariologado,
    evolucao,
    pas,
    pad,
    fc,
    fr,
    sao2,
    tax,
    diu,
    fezes,
    bh,
    acv,
    ar,
    abdome,
    outros,
    glasgow,
    rass,
    ramsay,
    hd,
    uf,
    heparina,
  }) {
  //servidor.
  var html = 'https://pulsarapp-server.herokuapp.com';
  const changeState = useDispatch();

  // chave para exibição do componente.
  const [viewcomponent, setviewcomponent] = useState(viewevolucao);

  // variáveis associadas à evolução:
  const [hepa, sethepa] = useState('');
  const [poop, setpoop] = useState('');
  useEffect(() => {
    if (viewevolucao === 1) {
      setviewcomponent(viewevolucao);
      setviewhd(0);
      sethepa(0);
    } else if (viewevolucao === 2) {
      setviewcomponent(viewevolucao);
      setpoop(fezes);
      if (hd === 1) {
        setviewhd(1);
        sethepa(heparina);
      } else {
        setviewhd(0);
        sethepa(0);
      }
    }
  }, [viewevolucao])

  // inserindo registro.
  const insertData = () => {
    // var evolucao
    if (evolucao !== '') {
      var obj = {
        idatendimento: idatendimento,
        data: moment().format('DD/MM/YYYY - HH:mm'),
        evolucao: document.getElementById('inputEvolucao').value.toUpperCase(),
        pas: document.getElementById('inputPas').value,
        pad: document.getElementById('inputPad').value,
        fc: document.getElementById('inputFc').value,
        fr: document.getElementById('inputFr').value,
        sao2: document.getElementById('inputSao2').value,
        tax: document.getElementById('inputTax').value,
        diu: document.getElementById('inputDiurese').value,
        fezes: poop,
        bh: document.getElementById('inputBh').value,
        acv: document.getElementById('inputAcv').value,
        ar: document.getElementById('inputAr').value,
        abdome: document.getElementById('inputAbdome').value,
        outros: document.getElementById('inputOutros').value,
        glasgow: document.getElementById('inputGlasgow').value,
        rass: document.getElementById('inputRass').value,
        ramsay: document.getElementById('inputRamsay').value,
        uf: document.getElementById('inputUf').value,
        heparina: hepa,
        idusuario: idusuario,
        funcao: funcao,
        usuario: usuario,
      };
      axios.post(html + '/insertevolucao', obj);
      toast(1, '#52be80', 'EVOLUÇÃO REGISTRADA COM SUCESSO.', 6000);
      setTimeout(() => {
        setviewcomponent(0);
      }, 6000);
    } else {
      toast(1, '#ec7063', 'CAMPOS OBRIGATÓRIOS EM BRANCO.', 6000);
    }
  };

  // inserindo registro.
  const updateData = () => {
    if (evolucao !== '') {
      var obj = {
        idatendimento: idatendimento,
        data: data,
        evolucao: document.getElementById('inputEvolucao').value.toUpperCase(),
        pas: document.getElementById('inputPas').value,
        pad: document.getElementById('inputPad').value,
        fc: document.getElementById('inputFc').value,
        fr: document.getElementById('inputFr').value,
        sao2: document.getElementById('inputSao2').value,
        tax: document.getElementById('inputTax').value,
        diu: document.getElementById('inputDiurese').value,
        fezes: poop,
        bh: document.getElementById('inputBh').value,
        acv: document.getElementById('inputAcv').value,
        ar: document.getElementById('inputAr').value,
        abdome: document.getElementById('inputAbdome').value,
        outros: document.getElementById('inputOutros').value,
        glasgow: document.getElementById('inputGlasgow').value,
        rass: document.getElementById('inputRass').value,
        ramsay: document.getElementById('inputRamsay').value,
        uf: document.getElementById('inputUf').value,
        heparina: hepa,
        idusuario: idusuario,
        funcao: funcao,
        usuario: usuario,
      };
      axios.post(html + '/updatevolucao/' + idevolucao, obj);
      toast(1, '#52be80', 'EVOLUÇÃO ATUALIZADA COM SUCESSO.', 6000);
      setTimeout(() => {
        setviewcomponent(0);
      }, 6000);
    } else {
      toast(1, '#ec7063', 'CAMPOS OBRIGATÓRIOS EM BRANCO.', 6000);
    }
  };

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time + 1000);
  }

  // validando entradas para campos numéricos.
  const validatePas = (txt) => {
    var last = txt.slice(-1);
    if (isNaN(last) === true) {
      last = '';
      document.getElementById('inputPas').value = '';
    } else {
    }
  };
  const validatePad = (txt) => {
    var last = txt.slice(-1);
    if (isNaN(last) === true) {
      last = '';
      document.getElementById('inputPad').value = '';
    } else {
    }
  };
  const validateFc = (txt) => {
    var last = txt.slice(-1);
    if (isNaN(last) === true) {
      last = '';
      document.getElementById('inputFc').value = '';
    } else {
    }
  };
  const validateFr = (txt) => {
    var last = txt.slice(-1);
    if (isNaN(last) === true) {
      last = '';
      document.getElementById('inputFr').value = '';
    } else {
    }
  };
  const validateSao2 = (txt) => {
    var last = txt.slice(-1);
    if (isNaN(last) === true) {
      last = '';
      document.getElementById('inputSao2').value = '';
    } else {
    }
  };
  const validateTax = (txt) => {
    var number = /[0-9]/;
    var dot = /[.]/;
    var last = txt.slice(-1);
    if (last.match(number) !== null || last.match(dot) !== null) {
    } else {
      document.getElementById('inputTax').value = '';
    }
  };
  const validateDiu = (txt) => {
    var last = txt.slice(-1);
    if (isNaN(last) === true) {
      last = '';
      document.getElementById('inputDiurese').value = '';
    } else {
    }
  };
  const validateBh = (txt) => {
    var number = /[0-9]/;
    var sign = /[-]/;
    var last = txt.slice(-1);
    if (last.match(number) !== null || last.match(sign) !== null) {
    } else {
      document.getElementById('inputBh').value = '';
    }
  };

  const [viewhd, setviewhd] = useState(0);
  function Hemodialise() {
    if (viewhd === 1) {
      return (
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            marginTop: 5
          }}>
          <input
            className="input"
            autoComplete="off"
            placeholder="UF"
            title="ULTRAFILTRADO (PERDAS EM ML)."
            defaultValue={uf}
            onFocus={(e) => (e.target.placeholder = '')}
            onBlur={(e) => (e.target.placeholder = 'UF')}
            onChange={(e) => validateBh(e.target.value)}
            style={{
              height: 50,
              width: 100,
              margin: 0,
              padding: 0,
            }}
            id="inputUf"
            maxLength={4}
          ></input>
          <button
            className={hepa === 1 ? "red-button" : "blue-button"}
            title="USO DE HEPARINA NA DIÁLISE."
            defaultValue={hepa}
            onClick={() => hepa === 1 ? sethepa(0) : sethepa(1)}
            style={{
              padding: 10,
              margin: 0,
              marginLeft: 10,
              width: 100,
              height: 50,
              backgroundColor: hepa === 1 ? '#ec7063' : '#1f7a8c'
            }}
          >
            HEPARINA
          </button>
        </div>
      )
    } else {
      return null;
    }
  }

  // renderização do componente.
  if (viewcomponent === 1) { // inserir.
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          //height: '100%',
          //width: '100%',
          margin: 0,
          zIndex: 5,
        }}
      >
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            padding: 30,
          }}
        >
          <label className="title2" style={{ marginTop: 0 }}>
            INSERIR EVOLUÇÃO
          </label>
          <div
            className="scroll"
            style={{
              justifyContent: 'flex-start',
              backgroundColor: '#F1F3F9',
              borderColor: '#F1F3F9',
              height: 350,
              margin: 0,
              paddingRight: 5,
            }}
          >
            <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
              EVOLUÇÃO:
            </label>
            <textarea
              autoComplete="off"
              className="textarea"
              placeholder="EVOLUÇÃO."
              onFocus={(e) => (e.target.placeholder = '')}
              onBlur={(e) => (e.target.placeholder = 'EVOLUÇÃO.')}
              title="EVOLUÇÃO."
              style={{
                width: 0.4 * window.innerWidth,
                minWidth: 400,
                minHeight: 125,
              }}
              type="text"
              maxLength={200}
              id="inputEvolucao"
            ></textarea>
            <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
              SENSÓRIO:
            </label>
            <div style={{ display: 'flex', flexDirection: 'row' }}>
              <input
                className="input"
                autoComplete="off"
                placeholder="GLASGOW"
                title="GLASGOW"
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = 'GLASGOW')}
                style={{
                  minHeight: 50,
                  width: 100,
                  textAlign: 'center',
                }}
                min={3}
                max={15}
                id="inputGlasgow"
                maxLength={2}
              ></input>
              <input
                className="input"
                autoComplete="off"
                placeholder="RASS"
                title="RASS"
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = 'RASS')}
                style={{
                  minHeight: 50,
                  width: 100,
                  textAlign: 'center',
                }}
                min={-5}
                max={4}
                id="inputRass"
                maxLength={2}
              ></input>
              <input
                className="input"
                autoComplete="off"
                placeholder="RAMSAY"
                title="RAMSAY"
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = 'RAMSAY')}
                style={{
                  minHeight: 50,
                  width: 100,
                  textAlign: 'center',
                }}
                min={1}
                max={6}
                id="inputRamsay"
                maxLength={1}
              ></input>
            </div>
            <div
              style={{
                display: 'flex',
                flexDirection: 'row',
              }}
            >
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  PAS:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="PAS"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'PAS')}
                  onChange={(e) => validatePas(e.target.value)}
                  title="PRESSÃO ARTERIAL SISTÓLICA."
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 0,
                  }}
                  id="inputPas"
                  maxLength={3}
                ></input>
              </div>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  PAD:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="PAD"
                  title="PRESSÃO ARTERIAL DIASTÓLICA."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'PAD')}
                  onChange={(e) => validatePad(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 5,
                  }}
                  id="inputPad"
                  maxLength={3}
                ></input>
              </div>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  FC:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="FC"
                  title="FREQUÊNCIA CARDÍACA."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'FC')}
                  onChange={(e) => validateFc(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 5,
                  }}
                  id="inputFc"
                  maxLength={3}
                ></input>
              </div>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  FR:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="FR"
                  title="FREQUÊNCIA RESPIRATÓRIA."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'FR')}
                  onChange={(e) => validateFr(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 5,
                  }}
                  id="inputFr"
                  maxLength={2}
                ></input>
              </div>
            </div>
            <div style={{ display: 'flex', flexDirection: 'row', marginTop: 5 }}>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  TAX:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="TAX"
                  title="TEMPERATURA AXILAR."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'TAX')}
                  onChange={(e) => validateTax(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 0,
                  }}
                  id="inputTax"
                  maxLength={4}
                ></input>
              </div>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  SAO2:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="SAO2"
                  title="SAO2."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'SAO2')}
                  onChange={(e) => validateSao2(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 5,
                  }}
                  id="inputSao2"
                  min={0}
                  max={100}
                ></input>
              </div>
            </div>
            <div
              style={{
                display: 'flex',
                flexDirection: 'column',
                borderRadius: 5,
                marginTop: 5,
                marginBottom: 0,
              }}
            >
              <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
                EVACUAÇÕES:
              </label>
              <div style={{ display: 'flex', flexDirection: 'row' }}>
                <button
                  className="blue-button"
                  onClick={() => setpoop('AUSENTES')}
                  style={{
                    padding: 10,
                    marginTop: 5,
                    marginRight: 10,
                    width: 100,
                    backgroundColor: poop === 'AUSENTES' ? '#ec7063' : '#1f7a8c',
                  }}
                >
                  AUSENTES
                </button>
                <button
                  className="blue-button"
                  onClick={() => setpoop('NORMAIS')}
                  style={{
                    padding: 10,
                    marginTop: 5,
                    marginRight: 10,
                    width: 100,
                    backgroundColor: poop === 'NORMAIS' ? '#ec7063' : '#1f7a8c',
                  }}
                >
                  NORMAIS
                </button>
                <button
                  className="blue-button"
                  onClick={() => setpoop('DIARRÉIA')}
                  style={{
                    padding: 10,
                    marginTop: 5,
                    marginRight: 10,
                    width: 100,
                    backgroundColor: poop === 'DIARRÉIA' ? '#ec7063' : '#1f7a8c',
                  }}
                >
                  DIARRÉIA
                </button>
              </div>
            </div>
            <div style={{ display: 'flex', flexDirection: 'row' }}>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  DIURESE:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="DIURESE"
                  title="DIURESE."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'DIURESE')}
                  onChange={(e) => validateDiu(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                  }}
                  id="inputDiurese"
                  maxLength={4}
                ></input>
              </div>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  BH:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="BH"
                  title="BALANÇO HÍDRICO."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'BH')}
                  onChange={(e) => validateBh(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 5,
                  }}
                  id="inputBh"
                  maxLength={5}
                ></input>
              </div>
            </div>
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', marginTop: 15 }}>
              <button
                className="blue-button"
                onClick={() => viewhd === 1 ? setviewhd(0) : setviewhd(1)}
                title="HEMODIÁLISE."
                style={{
                  padding: 10,
                  marginTop: 5,
                  marginRight: viewhd === 1 ? 10 : 0,
                  backgroundColor: viewhd === 1 ? '#ec7063' : '#1f7a8c',
                }}
              >
                HD
            </button>
              <Hemodialise></Hemodialise>
            </div>
            <div style={{ display: 'flex', flexDirection: 'row' }}>
              <div>
                <label
                  className="title2"
                  style={{
                    marginTop: 15,
                    fontSize: 14,
                    justifyContent: 'left',
                    paddingLeft: 10,
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                  }}
                >
                  APARELHO CARDIOVASCULAR:
                </label>
                <textarea
                  className="textarea"
                  autoComplete="off"
                  placeholder="APARELHO CARDIOVASCULAR."
                  title="APARELHO CARDIOVASCULAR."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'ACV.')}
                  style={{
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                    height: 80,
                    marginRight: 10,
                  }}
                  type="text"
                  id="inputAcv"
                  maxLength={50}
                ></textarea>
              </div>
              <div>
                <label
                  className="title2"
                  style={{
                    marginTop: 15,
                    fontSize: 14,
                    justifyContent: 'left',
                    paddingLeft: 10,
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                  }}
                >
                  APARELHO RESPIRATÓRIO:
                </label>
                <textarea
                  className="textarea"
                  autoComplete="off"
                  placeholder="APARELHO RESPIRATÓRIO."
                  title="APARELHO RESPIRATÓRIO."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'AR.')}
                  style={{
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                    height: 80,
                  }}
                  type="text"
                  id="inputAr"
                  maxLength={50}
                ></textarea>
              </div>
            </div>
            <div style={{ display: 'flex', flexDirection: 'row', marginBottom: 5 }}>
              <div>
                <label
                  className="title2"
                  style={{
                    marginTop: 15,
                    fontSize: 14,
                    justifyContent: 'left',
                    paddingLeft: 10,
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                  }}
                >
                  ABDOME:
                </label>
                <textarea
                  className="textarea"
                  autoComplete="off"
                  placeholder="ABDOME."
                  title="ABDOME."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'ABDOME.')}
                  style={{
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                    height: 80,
                    marginRight: 10,
                  }}
                  type="text"
                  id="inputAbdome"
                  maxLength={50}
                ></textarea>
              </div>
              <div>
                <label
                  className="title2"
                  style={{
                    marginTop: 15,
                    fontSize: 14,
                    justifyContent: 'left',
                    paddingLeft: 10,
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                  }}
                >
                  OUTROS:
                </label>
                <textarea
                  className="textarea"
                  autoComplete="off"
                  placeholder="OUTROS."
                  title="OUTROS DADOS CLÍNICOS IMPORTANTES."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'OUTROS.')}
                  style={{
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                    height: 80,
                  }}
                  type="text"
                  id="inputOutros"
                  maxLength={50}
                ></textarea>
              </div>
            </div>
          </div>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: 25,
              marginBottom: 0,
            }}
          >
            <button className="red-button" onClick={() => setviewcomponent(0)}>
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
            <button className="green-button" onClick={() => insertData()}>
              <img
                alt=""
                src={salvar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else if (viewcomponent === 2 && idusuariologado === idusuario) {
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          height: '100%',
          width: '100%',
          margin: 0,
          zIndex: 5,
        }}
      >
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            padding: 30,
          }}
        >
          <label className="title2" style={{ marginTop: 0 }}>
            ATUALIZAR EVOLUÇÃO
          </label>
          <div
            className="scroll"
            style={{
              justifyContent: 'flex-start',
              backgroundColor: '#F1F3F9',
              borderColor: '#F1F3F9',
              height: 350,
              margin: 0,
              paddingRight: 5,
            }}
          >
            <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
              EVOLUÇÃO:
            </label>
            <textarea
              autoComplete="off"
              className="textarea"
              placeholder="EVOLUÇÃO."
              onFocus={(e) => (e.target.placeholder = '')}
              onBlur={(e) => (e.target.placeholder = 'EVOLUÇÃO.')}
              defaultValue={evolucao}
              title="EVOLUÇÃO."
              style={{
                width: 0.4 * window.innerWidth,
                minWidth: 400,
                minHeight: 125,
              }}
              type="text"
              maxLength={200}
              id="inputEvolucao"
            ></textarea>
            <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
              SENSÓRIO:
            </label>
            <div style={{ display: 'flex', flexDirection: 'row' }}>
              <input
                className="input"
                autoComplete="off"
                placeholder="GLASGOW"
                title="GLASGOW"
                defaultValue={glasgow}
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = 'GLASGOW')}
                style={{
                  minHeight: 50,
                  width: 100,
                  textAlign: 'center',
                }}
                min={3}
                max={15}
                id="inputGlasgow"
                maxLength={2}
              ></input>
              <input
                className="input"
                autoComplete="off"
                placeholder="RASS"
                title="RASS"
                defaultValue={rass}
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = 'RASS')}
                style={{
                  minHeight: 50,
                  width: 100,
                  textAlign: 'center',
                }}
                min={-5}
                max={4}
                id="inputRass"
                maxLength={2}
              ></input>
              <input
                className="input"
                autoComplete="off"
                placeholder="RAMSAY"
                title="RAMSAY"
                defaultValue={ramsay}
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = 'RAMSAY')}
                style={{
                  minHeight: 50,
                  width: 100,
                  textAlign: 'center',
                }}
                min={1}
                max={6}
                id="inputRamsay"
                maxLength={1}
              ></input>
            </div>
            <div
              className="linegroup"
            >
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  PAS:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="PAS"
                  defaultValue={pas}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'PAS')}
                  onChange={(e) => validatePas(e.target.value)}
                  title="PRESSÃO ARTERIAL SISTÓLICA."
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 0,
                  }}
                  id="inputPas"
                  maxLength={3}
                ></input>
              </div>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  PAD:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="PAD"
                  defaultValue={pad}
                  title="PRESSÃO ARTERIAL DIASTÓLICA."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'PAD')}
                  onChange={(e) => validatePad(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 5,
                  }}
                  id="inputPad"
                  maxLength={3}
                ></input>
              </div>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  FC:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="FC"
                  defaultValue={fc}
                  title="FREQUÊNCIA CARDÍACA."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'FC')}
                  onChange={(e) => validateFc(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 5,
                  }}
                  id="inputFc"
                  maxLength={3}
                ></input>
              </div>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  FR:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="FR"
                  defaultValue={fr}
                  title="FREQUÊNCIA RESPIRATÓRIA."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'FR')}
                  onChange={(e) => validateFr(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 5,
                  }}
                  id="inputFr"
                  maxLength={2}
                ></input>
              </div>
            </div>
            <div style={{ display: 'flex', flexDirection: 'row', marginTop: 5 }}>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  TAX:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="TAX"
                  title="TEMPERATURA AXILAR."
                  defaultValue={tax}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'TAX')}
                  onChange={(e) => validateTax(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 0,
                  }}
                  id="inputTax"
                  maxLength={4}
                ></input>
              </div>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  SAO2:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="SAO2"
                  title="SAO2."
                  defaultValue={sao2}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'SAO2')}
                  onChange={(e) => validateSao2(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 5,
                  }}
                  id="inputSao2"
                  min={0}
                  max={100}
                ></input>
              </div>
            </div>
            <div
              style={{
                display: 'flex',
                flexDirection: 'column',
                borderRadius: 5,
                marginTop: 5,
                marginBottom: 0,
              }}
            >
              <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
                EVACUAÇÕES:
              </label>
              <div style={{ display: 'flex', flexDirection: 'row' }}>
                <button
                  className="blue-button"
                  onClick={() => setpoop('AUSENTES')}
                  style={{
                    padding: 10,
                    marginTop: 5,
                    marginRight: 10,
                    width: 100,
                    backgroundColor: poop === 'AUSENTES' ? '#ec7063' : '#1f7a8c',
                  }}
                >
                  AUSENTES
                </button>
                <button
                  className="blue-button"
                  onClick={() => setpoop('NORMAIS')}
                  style={{
                    padding: 10,
                    marginTop: 5,
                    marginRight: 10,
                    width: 100,
                    backgroundColor: poop === 'NORMAIS' ? '#ec7063' : '#1f7a8c',
                  }}
                >
                  NORMAIS
                </button>
                <button
                  className="blue-button"
                  onClick={() => setpoop('DIARRÉIA')}
                  style={{
                    padding: 10,
                    marginTop: 5,
                    marginRight: 10,
                    width: 100,
                    backgroundColor: poop === 'DIARRÉIA' ? '#ec7063' : '#1f7a8c',
                  }}
                >
                  DIARRÉIA
                </button>
              </div>
            </div>
            <div style={{ display: 'flex', flexDirection: 'row' }}>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  DIURESE:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="DIURESE"
                  title="DIURESE."
                  defaultValue={diu}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'DIURESE')}
                  onChange={(e) => validateDiu(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                  }}
                  id="inputDiurese"
                  maxLength={4}
                ></input>
              </div>
              <div>
                <label
                  className="title2"
                  style={{ marginTop: 15, fontSize: 14 }}
                >
                  BH:
                </label>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="BH"
                  title="BALANÇO HÍDRICO."
                  defaultValue={bh}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'BH')}
                  onChange={(e) => validateBh(e.target.value)}
                  style={{
                    height: 50,
                    width: 100,
                    marginBottom: 0,
                    marginLeft: 5,
                  }}
                  id="inputBh"
                  maxLength={5}
                ></input>
              </div>
            </div>
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', marginTop: 15 }}>
              <button
                className="blue-button"
                onClick={() => viewhd === 1 ? setviewhd(0) : setviewhd(1)}
                title="HEMODIÁLISE."
                style={{
                  padding: 10,
                  marginTop: 5,
                  marginRight: viewhd === 1 ? 10 : 0,
                  backgroundColor: viewhd === 1 ? '#ec7063' : '#1f7a8c',
                }}
              >
                HD
            </button>
              <Hemodialise></Hemodialise>
            </div>
            <div style={{ display: 'flex', flexDirection: 'row' }}>
              <div>
                <label
                  className="title2"
                  style={{
                    marginTop: 15,
                    fontSize: 14,
                    justifyContent: 'left',
                    paddingLeft: 10,
                    width: 0.2 * window.innerWidth,
                  }}
                >
                  APARELHO CARDIOVASCULAR:
                </label>
                <textarea
                  className="textarea"
                  autoComplete="off"
                  placeholder="APARELHO CARDIOVASCULAR."
                  title="APARELHO CARDIOVASCULAR."
                  defaultValue={acv}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'ACV.')}
                  style={{
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                    height: 80,
                    marginRight: 10,
                  }}
                  type="text"
                  id="inputAcv"
                  maxLength={50}
                ></textarea>
              </div>
              <div>
                <label
                  className="title2"
                  style={{
                    marginTop: 15,
                    fontSize: 14,
                    justifyContent: 'left',
                    paddingLeft: 10,
                    width: 0.2 * window.innerWidth,
                  }}
                >
                  APARELHO RESPIRATÓRIO:
                </label>
                <textarea
                  className="textarea"
                  autoComplete="off"
                  placeholder="APARELHO RESPIRATÓRIO."
                  title="APARELHO RESPIRATÓRIO."
                  defaultValue={ar}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'AR.')}
                  style={{
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                    height: 80,
                  }}
                  type="text"
                  id="inputAr"
                  maxLength={50}
                ></textarea>
              </div>
            </div>
            <div style={{ display: 'flex', flexDirection: 'row', marginBottom: 5 }}>
              <div>
                <label
                  className="title2"
                  style={{
                    marginTop: 15,
                    fontSize: 14,
                    justifyContent: 'left',
                    paddingLeft: 10,
                    width: 0.2 * window.innerWidth,
                  }}
                >
                  ABDOME:
                </label>
                <textarea
                  className="textarea"
                  autoComplete="off"
                  placeholder="ABDOME."
                  title="ABDOME."
                  defaultValue={abdome}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'ABDOME.')}
                  style={{
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                    height: 80,
                    marginRight: 10,
                  }}
                  type="text"
                  id="inputAbdome"
                  maxLength={50}
                ></textarea>
              </div>
              <div>
                <label
                  className="title2"
                  style={{
                    marginTop: 15,
                    fontSize: 14,
                    justifyContent: 'left',
                    paddingLeft: 10,
                    width: 0.2 * window.innerWidth,
                  }}
                >
                  OUTROS:
                </label>
                <textarea
                  className="textarea"
                  autoComplete="off"
                  placeholder="OUTROS."
                  title="OUTROS DADOS CLÍNICOS IMPORTANTES."
                  defaultValue={outros}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'OUTROS.')}
                  style={{
                    width: 0.2 * window.innerWidth,
                    minWidth: 200,
                    height: 80,
                  }}
                  type="text"
                  id="inputOutros"
                  maxLength={50}
                ></textarea>
              </div>
            </div>
          </div>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: 25,
              marginBottom: 0,
            }}
          >
            <button className="red-button" onClick={() => setviewcomponent(0)}>
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
            <button className="green-button" onClick={() => updateData()}>
              <img
                alt=""
                src={salvar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Evolucao;
