import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import deletar from '../images/deletar.svg';
import salvar from '../images/salvar.svg';
import Toast from './Toast';

function Diagnostico(
  { viewdiagnostico,
    idAtendimento,
    idDiagnostico,
    inicio,
    termino,
    cid,
    diagnostico,
    idUsuario,
  }) {
  //servidor.
  var html = 'https://pulsarapp-server.herokuapp.com';
  const changeState = useDispatch();
  // api para CID10:
  var cid10 = 'https://cid10-api.herokuapp.com/cid10';

  // chave para exibição do componente.
  const [viewcomponent, setviewcomponent] = useState(viewdiagnostico);

  // lista de diagnósticos.
  const [listdiagnosticos, setListdiagnosticos] = useState([]);
  const loadDiagnosticos = () => {
    axios.get(cid10).then((response) => {
      setListdiagnosticos(response.data);
    });
  }

  // filtros para cid e diagnósticos.
  const [filtercid, setfiltercid] = useState('');
  const [filterdiagnostico, setfilterdiagnostico] = useState('');
  var searchcid = '';
  var searchdiagnostico = '';
  var timeout = null;
  const [arraydiagnostico, setarraydiagnostico] = useState([]);
  const filterCid = () => {
    clearTimeout(timeout);
    document.getElementById("inputDiagnostico").value = '';
    searchdiagnostico = '';
    document.getElementById("inputCid").focus();
    searchcid = document.getElementById("inputCid").value.toUpperCase();
    timeout = setTimeout(() => {
      if (searchcid === '') {
        setarraydiagnostico([]);
        document.getElementById("inputCid").value = '';
        document.getElementById("inputCid").focus();
      } else {
        setfiltercid(document.getElementById("inputCid").value.toUpperCase());
        setarraydiagnostico(listdiagnosticos.filter(item => item.codigo.includes(searchcid) === true));
        document.getElementById("inputCid").value = searchcid;
        document.getElementById("inputCid").focus();
      }
    }, 3000);
  }
  const filterDiagnostico = () => {
    clearTimeout(timeout);
    document.getElementById("inputCid").value = '';
    searchcid = '';
    document.getElementById("inputDiagnostico").focus();
    searchdiagnostico = document.getElementById("inputDiagnostico").value.toUpperCase();
    timeout = setTimeout(() => {
      if (searchdiagnostico === '') {
        setarraydiagnostico([]);
        document.getElementById("inputDiagnostico").value = '';
        document.getElementById("inputDiagnostico").focus();
      } else {
        setfilterdiagnostico(document.getElementById("inputDiagnostico").value.toUpperCase());
        setarraydiagnostico(listdiagnosticos.filter(item => item.nome.toUpperCase().includes(searchdiagnostico) === true));
        document.getElementById("inputDiagnostico").value = searchdiagnostico;
        document.getElementById("inputDiagnostico").focus();
      }
    }, 3000);
  }

  useEffect(() => {
    if (viewdiagnostico !== 0) {
      // preparando datepicker.
      currentMonth();
      // carregando a lista de diagnósticos do sistema.
      loadDiagnosticos();
      setviewcomponent(viewdiagnostico);
    } else {
    }
  }, [viewdiagnostico])

  // inserindo registro.
  const insertData = () => {
    var cid = document.getElementById("inputCid").value.toUpperCase();
    var diagnostico = document.getElementById("inputDiagnostico").value.toUpperCase();
    if (dateInicio !== '' && cid !== '' && diagnostico !== '') {
      var obj = {
        idatendimento: idAtendimento,
        inicio: dateInicio,
        termino: dateTermino,
        cid: cid,
        diagnostico: diagnostico,
        idusuario: idUsuario,
      };
      axios.post(html + '/insertdiagnostico', obj);
      toast(1, '#52be80', 'DIAGNÓSTICO REGISTRADO COM SUCESSO.', 6000);
      setTimeout(() => {
        setviewcomponent(0);
      }, 4000);
    } else {
      toast(1, '#ec7063', 'CAMPOS OBRIGATÓRIOS EM BRANCO.', 6000);
    }
  };

  // atualizando registro.
  const updateData = () => {
    var cid = document.getElementById("inputCid").value.toUpperCase();
    var diagnostico = document.getElementById("inputDiagnostico").value.toUpperCase();
    if (dateInicio !== '' && cid !== '' && diagnostico !== '') {
      var obj = {
        id: idDiagnostico,
        idatendimento: idAtendimento,
        inicio: dateInicio,
        termino: dateTermino,
        cid: cid,
        diagnostico: diagnostico,
        idusuario: idUsuario,
      };
      axios.post(html + '/updatediagnostico/' + idAtendimento, obj);
      toast(1, '#52be80', 'DIAGNÓSTICO ATUALIZADO COM SUCESSO.', 6000);
      setTimeout(() => {
        setviewcomponent(0);
      }, 4000);
    } else {
      toast(1, '#ec7063', 'CAMPOS OBRIGATÓRIOS EM BRANCO.', 6000);
    }
  };

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time + 1000);
  }

  const selectDiagnostico = (item) => {
    document.getElementById("inputCid").value = item.codigo;
    document.getElementById("inputDiagnostico").value = item.nome;
    if (viewcomponent === 1) {
      insertData();
    } else {
      updateData();
    }
  }

  // DATEPICKER.
  // preparando a array com as datas.
  var arraydate = [];
  const [arraylist, setarraylist] = useState([]);
  // preparando o primeiro dia do mês.
  var month = moment().format('MM');
  var year = moment().format('YY');
  const [startdate] = useState(moment('01/' + month + '/' + year, 'DD/MM/YY'));
  // descobrindo o primeiro dia do calendário (último domingo do mês anteior).
  const firstSunday = (x, y) => {
    while (x.weekday() > 0) {
      x.subtract(1, 'day');
      y.subtract(1, 'day');
    }
    // se o primeiro domingo da array ainda cair no mês atual:
    if (x.month() == startdate.month()) {
      x.subtract(7, 'days');
      y.subtract(7, 'days');
    }
  }
  // criando array com 42 dias a partir da startdate.
  const setArrayDate = (x, y) => {
    arraydate = [x.format('DD/MM/YY')];
    while (y.diff(x, 'days') > 1) {
      x.add(1, 'day');
      arraydate.push(x.format('DD/MM/YY').toString());
    }
  }
  // criando a array de datas baseada no mês atual.
  const currentMonth = () => {
    var month = moment(startdate).format('MM');
    var year = moment(startdate).format('YY');
    var x = moment('01/' + month + '/' + year, 'DD/MM/YY');
    var y = moment('01/' + month + '/' + year, 'DD/MM/YY').add(42, 'days');
    firstSunday(x, y);
    setArrayDate(x, y);
    setarraylist(arraydate);
  }
  // percorrendo datas do mês anterior.
  const previousMonth = () => {
    startdate.subtract(30, 'days');
    var month = moment(startdate).format('MM');
    var year = moment(startdate).format('YY');
    var x = moment('01/' + month + '/' + year, 'DD/MM/YY');
    var y = moment('01/' + month + '/' + year, 'DD/MM/YY').add(42, 'days');
    firstSunday(x, y);
    setArrayDate(x, y);
    setarraylist(arraydate);
  }
  // percorrendo datas do mês seguinte.
  const nextMonth = () => {
    startdate.add(30, 'days');
    var month = moment(startdate).format('MM');
    var year = moment(startdate).format('YY');
    var x = moment('01/' + month + '/' + year, 'DD/MM/YY');
    var y = moment('01/' + month + '/' + year, 'DD/MM/YY').add(42, 'days');
    firstSunday(x, y);
    setArrayDate(x, y);
    setarraylist(arraydate);
  }

  const selectInputInicio = () => {
    setinputselect(1);
    setViewDatePicker(1);
  }
  const selectInputTermino = () => {
    setinputselect(2);
    setViewDatePicker(1);
  }

  // exibição do datepicker.
  const showDatePicker = () => {
    setViewDatePicker(1);
  }
  // selecionando uma data de início ou término no datepicker.
  const [inputselect, setinputselect] = useState(0); // 1 para input dataInicio, 2 para input dataTermino.
  const [pickdate, setpickdate] = useState('');
  const [dateInicio, setdateInicio] = useState(moment().format('DD/MM/YYYY'));
  const [dateTermino, setdateTermino] = useState('');
  const selectDate = (date) => {
    setpickdate(date);
    if (inputselect === 1) {
      setdateInicio(date);
    } else {
      setdateTermino(date);
    }
    setViewDatePicker(0);
  }

  const [viewDatePicker, setViewDatePicker] = useState(0);
  // renderização do datepicker.
  function DatePicker() {
    if (viewDatePicker === 1) {
      return (
        <div
          className="secondary fade-in"
          style={{
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            borderRadius: 0,
            position: 'absolute',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            margin: 0,
            zIndex: 5,
          }}
        >
          <div
            className="secondary"
            style={{
              position: 'absolute',
              backgroundColor: '#ffffff',
              zIndex: 5,
              padding: 15,
            }}>
            <div style={{
              display: 'flex',
              flexDirection: 'row',
              width: '395',
              alignItems: 'center',
              justifyContent: 'center',
              padding: 5,
            }}>
              <button
                className="blue-button"
                onClick={() => previousMonth()}
                id="previous"
                style={{
                  width: 50,
                  height: 50,
                  margin: 2.5,
                  color: '#ffffff',
                }}
                title={'MÊS ANTERIOR'}
              >
                {'◄'}
              </button>
              <p
                className="title2"
                style={{
                  width: 200,
                  fontSize: 16,
                  margin: 2.5
                }}>
                {startdate.format('MMMM').toUpperCase() + ' ' + startdate.year()}
              </p>
              <button
                className="blue-button"
                onClick={() => nextMonth()}
                id="next"
                style={{
                  width: 50,
                  height: 50,
                  margin: 2.5,
                  color: '#ffffff',
                }}
                title={'PRÓXIMO MÊS'}
              >
                {'►'}
              </button>
            </div>
            <div style={{
              display: 'flex',
              flexDirection: 'row',
              width: '395',
              alignItems: 'center',
              justifyContent: 'center',
              padding: 5,
            }}>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>DOM</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>SEG</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>TER</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>QUA</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>QUI</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>SEX</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>SAB</p>
            </div>
            <div
              className="secondary"
              id="LISTA DE DATAS"
              style={{
                display: 'flex',
                flexDirection: 'row',
                flexWrap: 'wrap',
                justifyContent: 'center',
                alignItems: 'center',
                borderRadius: 5,
                margin: 0,
                padding: 5,
                width: 395,
                height: 340,
              }}
            >
              {arraylist.map((item) => (
                <button
                  className="blue-button"
                  onClick={() => selectDate(item)}
                  style={{
                    width: 50,
                    height: 50,
                    margin: 2.5,
                    color: '#ffffff',
                    backgroundColor: item === pickdate ? '#ec7063' : '#1f7a8c',
                    opacity: item.substring(3, 5) === moment(startdate).format('MM') ? 1 : 0.5,
                  }}
                  title={item}
                >
                  {item.substring(0, 2)}
                </button>
              ))}
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // renderização do componente.
  if (viewcomponent === 1) { // inserir.
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          margin: 0,
          zIndex: 5,
        }}
      >
        <DatePicker></DatePicker>
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            padding: 30,
          }}
        >
          <label className="title2" style={{ marginTop: 0 }}>
            INSERIR DIAGNÓSTICO
          </label>
          <div
            className="scroll"
            style={{
              justifyContent: 'flex-start',
              backgroundColor: '#F1F3F9',
              borderColor: '#F1F3F9',
              margin: 0,
              paddingRight: 5,
            }}
          >
            <div id="INÍCIO E TÉRMINO DO DIAGNÓSTICO" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
                  INÍCIO:
                </label>
                <label
                  autoComplete="off"
                  className="input"
                  placeholder="INÍCIO"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'INÍCIO')}
                  title="INICIO."
                  onClick={() => selectInputInicio()}
                  defaultValue={dateInicio ? dateInicio : moment().format('DD/MM/YYYY')}
                  style={{
                    width: 150,
                    minWidth: 150,
                  }}
                  type="text"
                  maxLength={5}
                  id="inputInicio"
                >
                  {dateInicio}
                </label>
              </div>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
                  TÉRMINO:
                </label>
                <label
                  autoComplete="off"
                  className="input"
                  placeholder="TÉRMINO"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'TÉRMINO')}
                  onClick={() => selectInputTermino()}
                  title="TÉRMINO."
                  style={{
                    width: 150,
                    minWidth: 150,
                  }}
                  type="text"
                  maxLength={200}
                  id="inputTermino"
                >
                  {dateTermino}
                </label>
              </div>
            </div>
            <div id="FILTROS DE DIAGNÓSTICOS" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
                  CID:
                </label>
                <input
                  autoComplete="off"
                  className="input"
                  placeholder="CID..."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'CID...')}
                  onChange={() => filterCid()}
                  title="CID."
                  style={{
                    width: 100,
                    minWidth: 100,
                  }}
                  type="text"
                  maxLength={5}
                  id="inputCid"
                ></input>
              </div>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
                  DIAGNÓSTICO:
                </label>
                <input
                  autoComplete="off"
                  className="input"
                  placeholder="DIAGNÓSTICO..."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'DIAGNÓSTICO...')}
                  onChange={() => filterDiagnostico()}
                  title="DIAGNÓSTICO."
                  style={{
                    width: 0.3 * window.innerWidth,
                  }}
                  type="text"
                  maxLength={200}
                  id="inputDiagnostico"
                ></input>
              </div>
            </div>
            <div
              className="scroll"
              id="LISTA DE DIAGNÓSTICOS"
              style={{
                //visibility: arraydiagnostico.length > 0 ? 'visible' : 'hidden',
                backgroundColor: '#e1e5f2',
                borderColor: '#e1e5f2',
                display: 'flex',
                justifyContent: 'flex-start',
                margin: 5,
                marginTop: 5,
                marginBottom: 5,
                padding: 0,
                height: '20.5vh',
                width: 0.4 * window.innerWidth,
              }}
            >
              {arraydiagnostico.map((item) => (
                <p
                  key={item.id}
                  id="item da lista"
                  className="row"
                  onClick={() => selectDiagnostico(item)}
                  style={{ margin: 5, marginTop: 2.5, marginBottom: 2.5 }}
                >
                  <button
                    className="blue-button"
                    style={{
                      width: 100,
                      margin: 2.5,
                      flexDirection: 'column',
                    }}
                  >
                    <div>{item.codigo}</div>
                  </button>
                  <button
                    className="blue-button"
                    style={{
                      width: 0.3 * window.innerWidth,
                      margin: 2.5,
                      padding: 5,
                      flexDirection: 'column',
                    }}
                  >
                    <div>{item.nome.toUpperCase()}</div>
                  </button>
                </p>
              ))}
            </div>
          </div>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: 30,
              marginBottom: 0,
            }}
          >
            <button className="red-button" onClick={() => setviewcomponent(0)}>
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else if (viewcomponent === 2) { // atualizar.
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          margin: 0,
          zIndex: 5,
        }}
      >
        <DatePicker></DatePicker>
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            padding: 30,
          }}
        >
          <label className="title2" style={{ marginTop: 0 }}>
            ATUALIZAR DIAGNÓSTICO
            </label>
          <div
            className="scroll"
            style={{
              justifyContent: 'flex-start',
              backgroundColor: '#F1F3F9',
              borderColor: '#F1F3F9',
              margin: 0,
              paddingRight: 5,
            }}
          >
            <div id="INÍCIO E TÉRMINO DO DIAGNÓSTICO" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
                  INÍCIO:
                </label>
                <label
                  autoComplete="off"
                  className="input"
                  placeholder="INÍCIO"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'INÍCIO')}
                  title="INICIO."
                  onClick={() => selectInputInicio()}
                  defaultValue={pickdate}
                  style={{
                    width: 150,
                    minWidth: 150,
                  }}
                  type="text"
                  maxLength={5}
                  id="inputInicio"
                >
                  {dateInicio}
                </label>
              </div>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
                  TÉRMINO:
                  </label>
                <label
                  autoComplete="off"
                  className="input"
                  placeholder="TÉRMINO"
                  defaultValue={termino}
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'TÉRMINO')}
                  onClick={() => selectInputTermino()}
                  title="TÉRMINO."
                  style={{
                    width: 150,
                    minWidth: 150,
                  }}
                  type="text"
                  maxLength={200}
                  id="inputTermino"
                >
                  {dateTermino}
                </label>
              </div>
            </div>
            <div id="FILTROS DE DIAGNÓSTICOS" style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
                  CID:
                  </label>
                <input
                  autoComplete="off"
                  className="input"
                  placeholder="CID..."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'CID...')}
                  onChange={() => filterCid()}
                  title="CID."
                  defaultValue={cid}
                  style={{
                    width: 100,
                    minWidth: 100,
                  }}
                  type="text"
                  maxLength={5}
                  id="inputCid"
                ></input>
              </div>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
                  DIAGNÓSTICO:
                  </label>
                <input
                  autoComplete="off"
                  className="input"
                  placeholder="DIAGNÓSTICO..."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'DIAGNÓSTICO...')}
                  onChange={() => filterDiagnostico()}
                  title="DIAGNÓSTICO."
                  defaultValue={diagnostico}
                  style={{
                    width: 0.3 * window.innerWidth,
                  }}
                  type="text"
                  maxLength={200}
                  id="inputDiagnostico"
                ></input>
              </div>
            </div>
            <div
              className="scroll"
              id="LISTA DE DIAGNÓSTICOS"
              display={arraydiagnostico.length > 0 ? true : false}
              style={{
                //visibility: arraydiagnostico.length > 0 ? 'visible' : 'hidden',
                backgroundColor: '#e1e5f2',
                borderColor: '#e1e5f2',
                display: 'flex',
                justifyContent: 'flex-start',
                margin: 5,
                marginTop: 5,
                marginBottom: 5,
                padding: 0,
                height: '20.5vh',
                width: 0.4 * window.innerWidth,
              }}
            >
              {arraydiagnostico.map((item) => (
                <p
                  key={item.id}
                  id="item da lista"
                  className="row"
                  onClick={() => selectDiagnostico(item)}
                  style={{ margin: 5, marginTop: 2.5, marginBottom: 2.5 }}
                >
                  <button
                    className="blue-button"
                    style={{
                      width: 100,
                      margin: 2.5,
                      flexDirection: 'column',
                    }}
                  >
                    <div>{item.codigo}</div>
                  </button>
                  <button
                    className="blue-button"
                    style={{
                      width: 0.3 * window.innerWidth,
                      margin: 2.5,
                      padding: 5,
                      flexDirection: 'column',
                    }}
                  >
                    <div>{item.nome.toUpperCase()}</div>
                  </button>
                </p>
              ))}
            </div>
          </div>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: 25,
              marginBottom: 0,
            }}
          >
            <button className="red-button" onClick={() => setviewcomponent(0)}>
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Diagnostico;
