  dnpaciente: '',
  nomemae: '',
  enderecopaciente: '',
  telefonepaciente: '',
  emailpaciente: '',
  documentopaciente: '',
  statuspaciente: '', // ativo ou ocioso.

  // tabela atendimento.
  idatendimento: 0,
  leito: 0,
  dataadmissao: '',
  peso: 0,
  altura: 0,
  antecedentespessoais: '',
  alergias: '',
  medprev: '', // medicações de uso prévio ou contínuo.
  exprev: '', // exames prévios.
  hda: '', // história da doença atual.
  statusgravidade: '', // paciente instável, em alerta ou estável.
  statusatendimento: '', // ativo ou encerrado.

  // tabela diagnósticos.
  iddiagnostico: 0,
