// definindo reducer.
const reducerPulsar = function reducerPulsar(
  state = initialState,
  action
) {
  switch (action.type) {
    // tela inicial.
    case 'LOGIN_ON':
      return {
        // telas.
        login: 1,
        password: 0,
        recepcao: 0,
        secretaria: 0,
        atendimentos: 0,
        hospitais: 0,
        unidades: 0,
        pacientes: 0,
        escala: 0,
        movepaciente: 0,
        corrida: 0,
      };
    case 'LOGIN_OFF':
      return {
        // telas.
        login: 0,
        password: 0,
        recepcao: 0,
        atendimentos: 0,
        hospitais: 0,
        unidades: 0,
        pacientes: 0,
        escala: 0,
        movepaciente: 0,
        corrida: 0,
      };
    // qualuer tela >> tela password.
    case 'PASSWORD_ON':
      return {
        // telas.
        login: 0,
        password: 1,
        recepcao: 0,
        atendimentos: 0,
        hospitais: 0,
        unidades: 0,
        pacientes: 0,
        escala: 0,
        movepaciente: 0,
        corrida: 0,
        // payload login.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        funcao: action.payloadFuncao,
        tipo: action.payloadTipo,
      };
    // tela login >> tela secretária.
    case 'SECRETARIA_ON':
      return {
        // telas.
        login: 0,
        password: 0,
        recepcao: 0,
        secretaria: 1,
        atendimento: 0,
        hospitais: 0,
        unidades: 0,
        pacientes: 0,
        escala: 0,
        movepaciente: 0,
        corrida: 0,
        // payload login.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        tipo: action.payloadTipo,
      };
    // tela secretária >> tela atendimento.
    case 'ATENDIMENTO_ON':
      return {
        // telas.
        login: 0,
        password: 0,
        recepcao: 0,
        atendimentos: 1,
        hospitais: 0,
        unidades: 0,
        pacientes: 0,
        escala: 0,
        movepaciente: 0,
        corrida: 0,
        // payload login.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        tipo: action.payloadTipo,
      };
    // tela unidades >> tela escala.
    case 'ESCALA_ON':
      return {
        // telas.
        login: 0,
        password: 0,
        recepcao: 0,
        atendimentos: 0,
        hospitais: 0,
        unidades: 0,
        pacientes: 0,
        escala: 1,
        movepaciente: 0,
        corrida: 0,
        // payloads.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        tipo: action.payloadTipo,
        idhospital: action.payloadIdhospital,
        nomehospital: action.payloadNomehospital,
        idunidade: action.payloadIdunidade,
        nomeunidade: action.payloadNomeunidade,
      };
    // tela escala >> tela usuarios.
    case 'USUARIOS_ON':
      return {
        // telas.
        login: 0,
        password: 0,
        recepcao: 0,
        atendimentos: 0,
        hospitais: 0,
        unidades: 0,
        pacientes: 0,
        escala: 0,
        usuarios: 1,
        movepaciente: 0,
        corrida: 0,
        // payloads.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        tipo: action.payloadTipo,
        idhospital: action.payloadIdhospital,
        nomehospital: action.payloadNomehospital,
        idunidade: action.payloadIdunidade,
        nomeunidade: action.payloadNomeunidade,
      };
    // tela login >> tela hospitais.
    case 'HOSPITAL_ON':
      return {
        // telas.
        login: 0,
        password: 0,
        recepcao: 0,
        atendimentos: 0,
        hospitais: 1,
        unidades: 0,
        pacientes: 0,
        escala: 0,
        movepaciente: 0,
        corrida: 0,
        // payloads.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        funcao: action.payloadFuncao,
        tipo: action.payloadTipo,
      };
    // tela hospitais >> tela unidades.
    case 'UNIDADE_ON':
      return {
        // telas.
        login: 0,
        password: 0,
        recepcao: 0,
        atendimentos: 0,
        hospitais: 0,
        unidades: 1,
        pacientes: 0,
        escala: 0,
        movepaciente: 0,
        corrida: 0,
        // payloads.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        funcao: action.payloadFuncao,
        tipo: action.payloadTipo,
        nomehospital: action.payloadNomehospital,
      };
    // tela unidades >> tela pacientes.
    case 'PACIENTES_ON':
      return {
        // telas.
        login: 0,
        password: 0,
        recepcao: 0,
        atendimentos: 0,
        hospitais: 0,
        unidades: 0,
        pacientes: 1,
        escala: 0,
        usuarios: 0,
        movepaciente: 0,
        corrida: 0,
        // payloads.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        funcao: action.payloadFuncao,
        tipo: action.payloadTipo,
        idhospital: action.payloadIdhospital,
        nomehospital: action.payloadNomehospital,
        idunidade: action.payloadIdunidade,
        nomeunidade: action.payloadNomeunidade,
      };
    // tela corrida >> tela pacientes.
    case 'CORRIDA_OFF':
      return {
        corrida: 0,
        // payloads.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        funcao: action.payloadFuncao,
        tipo: action.payloadTipo,
        idhospital: action.payloadIdhospital,
        nomehospital: action.payloadNomehospital,
        nomeunidade: action.payloadNomeunidade,
        idatendimento: action.payloadIdatendimento,
      };
    case 'CORRIDA_ON':
      return {
        pacientes: 0,
        corrida: 1,
        // payloads.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        funcao: action.payloadFuncao,
        tipo: action.payloadTipo,
        idhospital: action.payloadIdhospital,
        nomehospital: action.payloadNomehospital,
        nomeunidade: action.payloadNomeunidade,
        idatendimento: action.payloadIdatendimento,
      };
    // tela corrida >> tela atualizar atendimento.
    case 'UPDATE_ATENDIMENTO_ON':
      return {
        corrida: 1,
        updateatendimento: 1,
        // payloads.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        tipo: action.payloadTipo,
        nomehospital: action.payloadNomehospital,
        nomeunidade: action.payloadNomeunidade,
        idatendimento: action.payloadIdatendimento,
      };
    case 'UPDATE_ATENDIMENTO_OFF':
      return {
        corrida: 1,
        updateatendimento: 0,
        // payloads.
        idusuario: action.payloadIdusuario,
        nomeusuario: action.payloadNomeusuario,
        tipo: action.payloadTipo,
        nomehospital: action.payloadNomehospital,
        nomeunidade: action.payloadNomeunidade,
        idatendimento: action.payloadIdatendimento,
      };
    default:
      return state;
  }
};

// criando a store.
const store = createStore(
  reducerPulsar,
  window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById('root')
);
