  // COMPONENTE PARA ALTERAÇÃO DO STATUS.
  const [viewstatus, setViewstatus] = useState(0);
  function ChangeStatus() {
    if (viewstatus === 1) {
      return (
        <div
          className="secondary fade-in"
          style={{
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            borderRadius: 0,
            position: 'absolute',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            height: '100%',
            width: '100%',
            marginTop: 0,
            marginBottom: 0,
            marginLeft: 0,
            marginRight: 0,
            zIndex: 9,
          }}
        >
          <div>
            <div
              className="secondary"
              style={{
                backgroundColor: '#FFFFFF',
                borderRadius: 5,
                paddingTop: 30,
                paddingBottom: 30,
                paddingLeft: 30,
                paddingRight: 30,
              }}
            >
              <label
                className="title2"
                style={{ marginTop: 0, marginBottom: 15 }}
              >
                ATUALIZAR STATUS
            </label>
              <div
                id="STATUS DO PACIENTE."
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  margin: 5,
                }}
              >
                <div
                  class="radio-toolbar"
                  style={{
                    display: 'flex',
                    flexDirection: 'row',
                    marginTop: 0,
                    marginBottom: 0,
                  }}
                >
                  <input
                    type="radio"
                    id="radio1"
                    name="status"
                    value="ESTÁVEL"
                    onClick={() => changeStatus(3)}
                  ></input>
                  <label for="radio1">ESTÁVEL</label>
                  <input
                    type="radio"
                    id="radio2"
                    name="status"
                    value="ALERTA"
                    onClick={() => changeStatus(2)}
                  ></input>
                  <label for="radio2">ALERTA</label>
                  <input
                    type="radio"
                    id="radio3"
                    name="status"
                    value="INSTÁVEL"
                    onClick={() => changeStatus(1)}
                  ></input>
                  <label for="radio3">INSTÁVEL</label>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  var stat = 0;
  const changeStatus = (value) => {
    setstatus(value);
    stat = value;
    updateAtendimento();
    setViewstatus(0);
  }

  // função que atualiza o paciente.
  const updateAtendimento = () => {
    var obj = {
      idpaciente: idpaciente,
      hospital: hospital,
      unidade: unidade,
      box: box,
      admissao: dataadmissao,
      nome: nome,
      dn: dn,
      peso: peso,
      altura: altura,
      antecedentes: antecedentes,
      alergias: alergias,
      medicacoes: medicacoes,
      exames: exames,
      historia: historia,
      status: stat,
      ativo: 1,
    };
    axios.post(html + '/updateatendimento/' + idatendimento, obj);
  };

  // COMPONENTE ÚLTIMA EVOLUÇÃO E EXAME FÍSICO.
  function Evolucoes() {
    var pam = Math.ceil((2 * parseInt(pad) + parseInt(pas)) / 3);
    return (
      <div
        id="EVOLUÇÃO E EXAME FÍSICO"
        title="ÚLTIMA EVOLUÇÃO E CONTROLES."
        className="widget"
        style={{
          width: 0.28 * (window.innerWidth),
          height: 305,
          margin: 2.5,
          flexDirection: 'column',
        }}>
        <div style={{ width: 280, color: '#1f7a8c' }}>{'EVOLUÇÃO - ' + dataevolucao + ':'}</div>
        <div style={{ width: 280 }}>{evolucao.toString().substring(0, 100) + '...'}</div>
        <div style={{ width: 280, color: '#1f7a8c', marginTop: 10 }}>{'CONTROLES:'}</div>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', width: 280 }}>
          <ViewGlasgow></ViewGlasgow>
          <ViewRass></ViewRass>
          <ViewRamsay></ViewRamsay>
        </div>
        <div style={{ color: pam < 70 ? '#ec7063' : '#ffffff' }}>{'PA: ' + pas + ' x ' + pad + ' (PAM ' + pam + ') mmHg'}</div>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', width: 190 }}>
          <div style={{ color: parseInt(fc) < 60 || parseInt(fc) > 120 ? '#ec7063' : '#ffffff' }} >{'FC: ' + fc + ' bpm'}</div>
          <div style={{ color: parseInt(fr) < 14 || parseInt(fr) > 26 ? '#ec7063' : '#ffffff' }} >{'FR: ' + fr + ' irpm'}</div>
        </div>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', width: 190 }}>
          <div style={{ color: parseInt(sao2) < 90 ? '#ec7063' : '#ffffff' }} >{'SAO2: ' + sao2 + ' %'}</div>
          <div style={{ color: parseFloat(tax) < 35.0 || parseFloat(tax) > 37.5 ? '#ec7063' : '#ffffff' }} >{'TAX: ' + tax + ' °C'}</div>
        </div>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-evenly', width: 280 }}>
          <div style={{ color: parseInt(diu) < 500 ? '#ec7063' : '#ffffff' }} >{'DIURESE: ' + diu + 'ml/12h'}</div>
          <div style={{ color: parseInt(bh) < -2000 || parseInt(bh) > 2000 ? '#ec7063' : '#ffffff' }} >{'BH: ' + bh + 'ml/12h'}</div>
        </div>
        <div style={{ color: parseInt(bha) < -2000 || parseInt(bha) > 2000 ? '#CB4335' : '#ffffff' }} >{'BH ACUMULADO: ' + bha + 'ml'}</div>
      </div>
    );
  }
