  // INVASÕES.
  // exibindo o fundo preto quando um menu de invasão é ativado.
  function ShowMenuCover() {
    if (
      vaMenu === 1 ||
      jidMenu === 1 ||
      jieMenu === 1 ||
      subcldMenu === 1 ||
      subcleMenu === 1 ||
      piardMenu === 1 ||
      piareMenu === 1 ||
      toraxMenu === 1 ||
      svdMenu === 1 ||
      abdMenu === 1 ||
      vfemdMenu === 1 ||
      vfemeMenu === 1 ||
      afemdMenu === 1 ||
      afemeMenu === 1 ||
      sncMenu === 1
    ) {
      return <div className="menucover"></div>;
    } else {
      return null;
    }
  }
  // ocultando os menus seletores de invasões.
  const hideInvasionsMenus = () => {
    setVaMenu(0);
    setJidMenu(0);
    setJieMenu(0);
    setSubcldMenu(0);
    setSubcleMenu(0);
    setPiardMenu(0);
    setPiareMenu(0);
    setToraxMenu(0);
    setAbdMenu(0);
    setVfemdMenu(0);
    setSvdMenu(0);
    setSncMenu(0);
  };

  // INVASÕES.
  // VIA AÉREA (VA).
  const [vaMenu, setVaMenu] = useState(0);
  function ShowVa() {
    // TQT
    if (va == 1) {
      return (
        <div
          className="orange-invasion va"
          title={'DATA DE INSERÇÃO: ' + datava}
          style={{ height: 0.02 * window.innerWidth, width: 0.02 * window.innerWidth }}
          onClick={() => clickVa()}
        >
          TQT
        </div>
      );
      // TOT
    } else if (va == 2) {
      return (
        <div
          className="orange-invasion va"
          title={'DATA DE INSERÇÃO: ' + datava + '\nDIAS DE TOT: ' + moment().diff(moment(datava, 'DD/MM/YY'), 'days')}
          style={{ height: 0.02 * window.innerWidth, width: 0.02 * window.innerWidth }}
          onClick={() => clickVa()}
        >
          TOT
        </div>
      );
      // MF
    } else if (va == 3) {
      return (
        <div
          className="orange-invasion va"
          title={'MÁSCARA FACIAL'}
          style={{ height: 0.02 * window.innerWidth, width: 0.02 * window.innerWidth }}
          onClick={() => clickVa()}
        >
          MF
        </div>
      );
      // CN
    } else if (va == 4) {
      return (
        <div
          className="orange-invasion va"
          title={'CATETER NASAL'}
          style={{ height: 0.02 * window.innerWidth, width: 0.02 * window.innerWidth }}
          onClick={() => clickVa()}
        >
          CN
        </div>
      );
    } else {
      return (
        <div
          className="orange-invasion va"
          title={'AR AMBIENTE'}
          style={{ height: 0.02 * window.innerWidth, width: 0.02 * window.innerWidth, opacity: 0.5 }}
          onClick={() => clickVa()}
        >
          AA
        </div>
      );
    }
  }
