  // DATEPICKER.
  // preparando a array com as datas.
  var arraydate = [];
  const [arraylist, setarraylist] = useState([]);
  // preparando o primeiro dia do mês.
  var month = moment().format('MM');
  var year = moment().format('YY');
  const [startdate] = useState(moment('01/' + month + '/' + year, 'DD/MM/YY'));
  // descobrindo o primeiro dia do calendário (último domingo do mês anteior).
  const firstSunday = (x, y) => {
    while (x.weekday() > 0) {
      x.subtract(1, 'day');
      y.subtract(1, 'day');
    }
    // se o primeiro domingo da array ainda cair no mês atual:
    if (x.month() == startdate.month()) {
      x.subtract(7, 'days');
      y.subtract(7, 'days');
    }
  }
  // criando array com 42 dias a partir da startdate.
  const setArrayDate = (x, y) => {
    arraydate = [x.format('DD/MM/YY')];
    while (y.diff(x, 'days') > 1) {
      x.add(1, 'day');
      arraydate.push(x.format('DD/MM/YY').toString());
    }
  }
  // criando a array de datas baseada no mês atual.
  const currentMonth = () => {
    var month = moment(startdate).format('MM');
    var year = moment(startdate).format('YY');
    var x = moment('01/' + month + '/' + year, 'DD/MM/YY');
    var y = moment('01/' + month + '/' + year, 'DD/MM/YY').add(42, 'days');
    firstSunday(x, y);
    setArrayDate(x, y);
    setarraylist(arraydate);
  }
  // percorrendo datas do mês anterior.
  const previousMonth = () => {
    startdate.subtract(30, 'days');
    var month = moment(startdate).format('MM');
    var year = moment(startdate).format('YY');
    var x = moment('01/' + month + '/' + year, 'DD/MM/YY');
    var y = moment('01/' + month + '/' + year, 'DD/MM/YY').add(42, 'days');
    firstSunday(x, y);
    setArrayDate(x, y);
    setarraylist(arraydate);
  }
  // percorrendo datas do mês seguinte.
  const nextMonth = () => {
    startdate.add(30, 'days');
    var month = moment(startdate).format('MM');
    var year = moment(startdate).format('YY');
    var x = moment('01/' + month + '/' + year, 'DD/MM/YY');
    var y = moment('01/' + month + '/' + year, 'DD/MM/YY').add(42, 'days');
    firstSunday(x, y);
    setArrayDate(x, y);
    setarraylist(arraydate);
  }
  // exibição do datepicker.
  const showDatePicker = () => {
    setViewDatePicker(1);
  }
  // selecionando uma data no datepicker.
  const selectDate = (date) => {
    setpickdate(date);
    setViewDatePicker(0);
  }
  // atualização das datas de implante dos dispositivos invasivos.
  const [pickdate, setpickdate] = useState('');
  const [viewDatePicker, setViewDatePicker] = useState(0);
  // renderização do datepicker.
  function DatePicker() {
    if (viewDatePicker === 1) {
      return (
        <div
          className="secondary"
          style={{
            position: 'absolute',
            backgroundColor: '#ffffff',
            zIndex: 5,
            padding: 15,
          }}>
          <div style={{
            display: 'flex',
            flexDirection: 'row',
            width: '395',
            alignItems: 'center',
            justifyContent: 'center',
            padding: 5,
          }}>
            <button
              className="blue-button"
              onClick={() => previousMonth()}
              id="previous"
              style={{
                width: 50,
                height: 50,
                margin: 2.5,
                color: '#ffffff',
              }}
              title={'MÊS ANTERIOR'}
            >
              {'◄'}
            </button>
            <p
              className="title2"
              style={{
                width: 200,
                fontSize: 16,
                margin: 2.5
              }}>
              {startdate.format('MMMM').toUpperCase() + ' ' + startdate.year()}
            </p>
            <button
              className="blue-button"
              onClick={() => nextMonth()}
              id="next"
              style={{
                width: 50,
                height: 50,
                margin: 2.5,
                color: '#ffffff',
              }}
              title={'PRÓXIMO MÊS'}
            >
              {'►'}
            </button>
          </div>
          <div style={{
            display: 'flex',
            flexDirection: 'row',
            width: '395',
            alignItems: 'center',
            justifyContent: 'center',
            padding: 5,
          }}>
            <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>DOM</p>
            <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>SEG</p>
            <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>TER</p>
            <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>QUA</p>
            <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>QUI</p>
            <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>SEX</p>
            <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>SAB</p>
          </div>
          <div
            className="secondary"
            id="LISTA DE DATAS"
            style={{
              display: 'flex',
              flexDirection: 'row',
              flexWrap: 'wrap',
              justifyContent: 'center',
              alignItems: 'center',
              borderRadius: 5,
              margin: 0,
              padding: 5,
              width: 395,
              height: 340,
            }}
          >
            {arraylist.map((item) => (
              <button
                className="blue-button"
                onClick={() => selectDate(item)}
                style={{
                  width: 50,
                  height: 50,
                  margin: 2.5,
                  color: '#ffffff',
                  backgroundColor: item === pickdate ? '#ec7063' : '#1f7a8c',
                  opacity: item.substring(3, 5) === moment(startdate).format('MM') ? 1 : 0.5,
                }}
                title={item}
              >
                {item.substring(0, 2)}
              </button>
            ))}
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // renderizando a tela INSERIR OU ATUALIZAR EVOLUÇÃO.
  const [viewevolucao, setviewevolucao] = useState();
  const viewEvolucao = (valor) => {
    if (viewevolucao === 1 || viewevolucao === 2) {
      setviewevolucao(0);
      setTimeout(() => {
        setviewevolucao(valor); // 1 para inserir evolução, 2 para atualizar evolução.
      }, 500);
    } else {
      setviewevolucao(valor); // 1 para inserir evolução, 2 para atualizar evolução.
    }
  }

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time + 1000);
  }

  // LISTA DE PRESCRIÇÕES.
  // constantes relacionadas à lista de prescricoes:
  const [idprescricao, setidprescricao] = useState(0);
  // constantes relacionadas à lista de items da prescrição:
  const [iditem, setiditem] = useState(0);
  const [grupo, setgrupo] = useState('');
  const [farmaco, setfarmaco] = useState('');
  const [qtdeitem, setqtdeitem] = useState(0);
  const [via, setvia] = useState('');
  const [horario, sethorario] = useState('');
  const [observacao, setobservacao] = useState('');
  // constantes relacionadas à lista de componentes
  const [codigo, setcodigo] = useState(0);
  const [idcomponente, setidcomponente] = useState(0);
  const [componente, setcomponente] = useState('');
  const [qtdecomponente, setqtdecomponente] = useState(0);

  // carregando a lista de prescrições.
  const [listprescricoes, setlistprescricoes] = useState([]);
  const loadPrescricoes = () => {
    axios.get(html + "/prescricoes/'" + idatendimento + "'").then((response) => {
      setlistprescricoes(response.data);
    });
  }

  // lista de itens disponíveis para inserção na prescrição.
  const [optionsitens, setoptionsitens] = useState([]);
  const loadOptionsItens = () => {
    axios.get(html + "/optionsitens").then((response) => {
      setoptionsitens(response.data);
    });
  }

  // filtro dos itens para prescrição.
  // 1. filtrando os itens já presentes na prescrição.
  const [filteritemprescricao, setfilteritemprescricao] = useState('');
  const [arrayitemprescricao, setarrayitemprescricao] = useState([]);
  // 2. filtrando opções de itens que poderão ser inseridos na prescrição.
  const [filteroptionsitens, setfilteroptionsitens] = useState('');
  const [arrayoptionsitens, setarrayoptionsitens] = useState([optionsitens]);
  var searchitemprescricao = '';
  var timeout = null;
  const filterItemPrescricao = () => {
    clearTimeout(timeout);
    document.getElementById("inputFilterItemPrescricao").focus();
    searchitemprescricao = document.getElementById("inputFilterItemPrescricao").value.toUpperCase();
    timeout = setTimeout(() => {
      if (searchitemprescricao === '') {
        setfilteritemprescricao('');
        setarrayitemprescricao(listitensprescricao);
        setarrayoptionsitens([]);
        document.getElementById("inputFilterItemPrescricao").value = '';
        document.getElementById("inputFilterItemPrescricao").focus();
      } else {
        setfilteritemprescricao(document.getElementById("inputFilterItemPrescricao").value.toUpperCase());
        setarrayitemprescricao(listitensprescricao.filter(item => item.farmaco.includes(searchitemprescricao) === true));
        setfilteroptionsitens(document.getElementById("inputFilterItemPrescricao").value.toUpperCase());
        setarrayoptionsitens(optionsitens.filter(item => item.farmaco.includes(searchitemprescricao) === true));
        document.getElementById("inputFilterItemPrescricao").value = searchitemprescricao;
        document.getElementById("inputFilterItemPrescricao").focus();
      }
    }, 3000);
  }

  // memorizando a posição da scroll na lista de prescriçoes.
  var scrollpos = 0;
  const [scrollposition, setscrollposition] = useState(0);
  var timeout;
  const scrollPosition = () => {
    clearTimeout(timeout);
    timeout = setTimeout(() => {
      scrollpos = document.getElementById('LISTA DE ITENS PRESCRITOS').scrollTop;
      setscrollposition(document.getElementById('LISTA DE ITENS PRESCRITOS').scrollTop);
      document.getElementById('LISTA DE ITENS PRESCRITOS').scrollTop = scrollpos;
    }, 1000);
  }
  const ugiBugi = () => {
    document.getElementById('LISTA DE ITENS PRESCRITOS').scrollTop = scrollposition;
  }

  // exibição da lista de prescrições.
  function ShowPrescricoes() {
    if (showprescricoes === 1) {
      return (
        <div id="prescricoes"
          style={{
            margin: 5,
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center'
          }}>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center', verticalAlign: 'center' }}>
            <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
              <Header></Header>
              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                <input
                  className="input"
                  autoComplete="off"
                  placeholder="PROCURAR..."
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = "PROCURAR...")}
                  onChange={() => filterItemPrescricao()}
                  disabled={idprescricao !== -1 ? false : true}
                  style={{
                    width: '100%',
                    margin: 5,
                    opacity: idprescricao !== -1 ? 1 : 0.3,
                  }}
                  type="text"
                  id="inputFilterItemPrescricao"
                  defaultValue={filteritemprescricao}
                  maxLength={100}
                ></input>
              </div>
              <div
                className="scrollheader"
                style={{
                  flexDirection: 'row',
                  justifyContent: 'flex-start',
                  margin: 0,
                  padding: 0,
                  paddingRight: 0,
                  height: 30,
                  minWidth: 0.60 * (window.innerWidth),
                  maxWidth: 0.60 * (window.innerWidth),
                }}>
                <button
                  className="header"
                  style={{
                    width: '100%',
                    margin: 2.5,
                    marginLeft: 5,
                    flexDirection: 'column',
                  }}
                >
                  ITEM:
                </button>
                <button
                  className="header"
                  style={{
                    width: 50,
                    margin: 2.5,
                    flexDirection: 'column',
                  }}
                >
                  QTDE:
                </button>
                <button
                  className="header"
                  style={{
                    width: 120,
                    minWidth: 120,
                    maxWidth: 120,
                    padding: 5,
                    margin: 2.5,
                    flexDirection: 'column',
                  }}
                >
                  VIA:
                </button>
                <button
                  className="header"
                  style={{
                    width: 120,
                    minWidth: 120,
                    maxWidth: 120,
                    margin: 2.5,
                    marginRight: 60,
                    flexDirection: 'column',
                  }}
                >
                  HORÁRIO:
                </button>
              </div>
              <div
                className="scroll"
                id="LISTA DE ITENS PRESCRITOS"
                onScroll={() => scrollPosition()}
                onLoad={() => ugiBugi()}
                style={{
                  justifyContent: 'flex-start',
                  margin: 5,
                  padding: 0,
                  paddingRight: 5,
                  height: 0.5 * window.innerHeight,
                  minWidth: 0.60 * (window.innerWidth),
                  maxWidth: 0.60 * (window.innerWidth),
                }}
              >
                {arrayitemprescricao.map((item) => (
                  <p
                    key={item.id}
                    id="item da prescrição"
                    className="row"
                    disabled={item.status === 1 || statusprescricao === 1 ? true : false}
                    style={{
                      margin: 5,
                      marginTop: 2.5,
                      marginBottom: 2.5,
                      flexDirection: 'column',
                      opacity: item.status === 1 ? 0.3 : 1,
                    }}
                  >
                    <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                      <button
                        className="blue-button"
                        onClick={() => selectItem(item)}
                        disabled={item.status === 1 || statusprescricao === 1 ? true : false}
                        style={{
                          width: '100%',
                          margin: 2.5,
                          marginLeft: 5,
                          flexDirection: 'column',
                          backgroundColor: '#1f7a8c',
                          opacity: item.id === iditem ? 1 : 0.8,
                        }}
                      >
                        <div style={{ textAlign: 'left', alignSelf: 'flex-start', padding: 10 }}>{item.farmaco}</div>
                      </button>
                      <input
                        className="blue-button"
                        disabled={item.status === 1 || statusprescricao === 1 ? true : false}
                        defaultValue={item.qtde}
                        autoComplete="off"
                        placeholder="QTDE."
                        onFocus={(e) => (e.target.placeholder = '')}
                        onBlur={(e) => (e.target.placeholder = 'QTDE.')}
                        title="QUANTIDADE (ITEM)."
                        style={{
                          display: item.grupo === 'CUIDADOS GERAIS' ? 'none' : 'flex',
                          width: 50,
                          margin: 2.5,
                          backgroundColor: '#e1e5f2',
                          color: '#1f7a8c',
                          flexDirection: 'column',
                          opacity: item.id === iditem ? 1 : 0.8,
                        }}
                        onChange={(e) => updateItemQtde(e.target.value, item)}
                        type="number"
                        id="inputItemQtde"
                        maxLength={3}>
                      </input>
                      <button
                        className="blue-button"
                        disabled={item.status === 1 || statusprescricao === 1 ? true : false}
                        onClick={() => clickItemVia(item)}
                        style={{
                          display: item.grupo === 'CUIDADOS GERAIS' ? 'none' : 'flex',
                          width: 120,
                          minWidth: 120,
                          maxWidth: 120,
                          padding: 5,
                          margin: 2.5,
                          flexDirection: 'column',
                          opacity: item.id === iditem ? 1 : 0.8,
                        }}
                      >
                        <div>{item.via}</div>
                      </button>
                      <button
                        className="blue-button"
                        disabled={item.status === 1 || statusprescricao === 1 ? true : false}
                        onClick={() => clickItemHorario(item)}
                        style={{
                          width: 120,
                          minWidth: 120,
                          maxWidth: 120,
                          margin: 2.5,
                          flexDirection: 'column',
                          opacity: item.id === iditem ? 1 : 0.8,
                        }}
                      >
                        <div>{item.horario}</div>
                      </button>
                      <button className="red-button"
                        onClick={() => deleteItem(item)}
                        disabled={item.status === 1 || statusprescricao === 1 ? true : false}
                        style={{
                          marginRight: 0,
                          display: statusprescricao === 0 ? 'flex' : 'none',
                        }}
                      >
                        <img
                          alt=""
                          src={deletar}
                          style={{
                            margin: 10,
                            height: 30,
                            width: 30,
                          }}
                        ></img>
                      </button>
                      <button className="red-button"
                        onClick={() => suspendItem(item)}
                        disabled={item.status === 1 ? true : false}
                        title={item.status === 1 ? "" : "SUSPENDER ITEM"}
                        style={{
                          marginRight: 0,
                          display: statusprescricao === 1 ? 'flex' : 'none',
                        }}
                      >
                        <img
                          alt=""
                          src={suspender}
                          style={{
                            margin: 10,
                            height: 30,
                            width: 30,
                          }}
                        ></img>
                      </button>
                    </div>
                    <div style={{ display: expanditem === 1 && item.id === iditem ? 'flex' : 'none', flexDirection: 'row', justifyContent: 'center', width: '100%' }}>
                      <div style={{ display: 'flex', flexDirection: 'column' }}>
                        <div className="title2" style={{ fontSize: 14 }}>OBSERVAÇÕES</div>
                        <textarea
                          id="inputObservacoes"
                          className="textarea"
                          disabled={item.status === 1 || statusprescricao === 1 ? true : false}
                          defaultValue={item.observacao}
                          onMouseLeave={(e) => updateObservacoes(e.target.value, item)}
                          style={{
                            width: 150,
                            margin: 5,
                            width: 250,
                            height: 185,
                            justifyContent: 'flex-start',
                          }}
                        >
                        </textarea>
                      </div>
                      <div style={{ display: 'flex', flexDirection: 'column', width: '100%', marginRight: 5 }}>
                        <div className="title2" style={{ fontSize: 14 }}>COMPONENTES</div>
                        <div
                          className="scroll"
                          disabled={item.status === 1 || statusprescricao === 1 ? true : false}
                          id="LISTA DE COMPONENTES"
                          style={{
                            backgroundColor: 'white',
                            borderColor: 'white',
                            display: 'flex',
                            justifyContent: 'flex-start',
                            margin: 5,
                            marginRight: 0,
                            padding: 0,
                            paddingRight: 5,
                            height: 185,
                            width: '100%',
                          }}
                        >
                          {FilterComponents(item.id).map((item) => (
                            <div
                              key={item.id}
                              id="componente do item da prescrição"
                              className="row"
                              style={{ margin: 0, marginTop: 2.5, flexDirection: 'column' }}
                            >
                              <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                                <button
                                  className="blue-button"
                                  style={{
                                    width: '100%',
                                    margin: 2.5,
                                    marginLeft: 5,
                                    flexDirection: 'column',
                                  }}
                                >
                                  <div>{item.componente}</div>
                                </button>
                                <input
                                  className="blue-button"
                                  disabled={item.status === 1 ? true : false}
                                  autoComplete="off"
                                  placeholder="QTDE."
                                  onFocus={(e) => (e.target.placeholder = '')}
                                  onBlur={(e) => (e.target.placeholder = 'QTDE')}
                                  onChange={(e) => updateComponenteQtde(e.target.value, item)}
                                  style={{
                                    backgroundColor: '#e1e5f2',
                                    color: '#1f7a8c',
                                    width: 50,
                                  }}
                                  defaultValue={item.quantidade}
                                  type="number"
                                  id="inputComponenteQtde"
                                  title="QUANTIDADE (COMPONENTE)."
                                  maxLength={3}>
                                </input>
                                <button className="red-button"
                                  onClick={() => deleteComponent(item)}
                                  disabled={item.status === 1 ? true : false}
                                  style={{ marginRight: 0 }}
                                >
                                  <img
                                    alt=""
                                    src={deletar}
                                    style={{
                                      margin: 10,
                                      height: 30,
                                      width: 30,
                                    }}
                                  ></img>
                                </button>
                              </div>
                            </div>
                          ))}
                          <button className="green-button"
                            disabled={item.status === 1 ? true : false}
                            style={{ display: 'flex', alignSelf: 'flex-end', width: 50, marginTop: 6, marginRight: 5 }}
                          >
                            <img
                              alt=""
                              src={novo}
                              onClick={() => viewInsertComponente()}
                              style={{
                                margin: 10,
                                height: 30,
                                width: 30,
                              }}
                            ></img>
                          </button>
                        </div>
                      </div>
                    </div>
                  </p>
                ))}
                <div className="title2"
                  style={{
                    color: '#ec7063',
                    fontSize: 14,
                    fontWeight: 'bold',
                    margin: 5,
                    marginTop: 10
                  }}>
                  {arrayoptionsitens.length === 1 && arrayitemprescricao.length > 0 && statusprescricao === 0 ? 'REPETIR ITEM...' :
                    arrayoptionsitens.length > 0 && arrayitemprescricao.length > 0 && statusprescricao === 0 ? 'INSERIR ITEM...' :
                      arrayoptionsitens.length > 0 && arrayitemprescricao.length < 1 && statusprescricao === 0 ? 'INSERIR ITEM...' : ''}
                </div>
                {arrayoptionsitens.map((item) => (
                  <p id="LISTA DE OPÇÕES DE ITENS">
                    <button className="green-button"
                      onClick={() => insertItem(item)}
                      style={{
                        display: statusprescricao === 0 ? 'flex' : 'none',
                        alignSelf: 'flex-end',
                        width: 300,
                        margin: 5,
                        marginLeft: 0
                      }}
                    >
                      {item.farmaco}
                    </button>
                  </p>
                ))}
              </div>
            </div>
            <div
              style={{
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
              }}>
              <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', height: 85 }}>
                <div className="title2" style={{ fontSize: 16 }}>LISTA DE PRESCRIÇÕES</div>
              </div>
              <div
                className="scroll"
                id="LISTA DE PRESCRIÇÕES"
                style={{
                  display: 'flex',
                  justifyContent: 'flex-start',
                  marginTop: 0,
                  marginLeft: 10,
                  marginBottom: 0,
                  padding: 0,
                  paddingRight: 5,
                  height: document.getElementById("menuview").offsetHeight,
                  width: 246,
                }}
              >
                {listprescricoes.map((item) => (
                  <div
                    key={item.id}
                    id="prescrição"
                    className="row"
                    style={{ margin: 5, marginTop: 2.5, marginBottom: 2.5, flexDirection: 'column' }}
                    onClick={() => selectPrescricao(item)}
                  >
                    <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
                      <button
                        className={idprescricao === item.id ? "red-button" : "blue-button"}
                        style={{
                          width: '100%',
                          margin: 2.5,
                          marginLeft: 5,
                          flexDirection: 'column',
                        }}
                      >
                        <div>{item.data}</div>
                        <div>{item.usuario}</div>
                        <div>{item.conselho}</div>
                      </button>
                      <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
                        <button className="red-button"
                          onClick={() => deletePrescription(item)}
                          disabled={moment().diff(moment(item.data, 'DD/MM/YY'), 'hours') < 24 || item.status === 0 ? false : true}
                          style={{
                            marginRight: 0,
                            opacity: moment().diff(moment(item.data, 'DD/MM/YY'), 'hours') < 24 || item.status === 0 ? 1 : 0.3,
                          }}
                          title={moment().diff(moment(item.data, 'DD/MM/YY'), 'hours') < 24 || item.status === 0 ? 'EXCLUIR PRESCRIÇÃO' : 'NÃO É POSSÍVEL EXCLUIR A PRESCRIÇÃO'}
                        >
                          <img
                            alt=""
                            src={deletar}
                            style={{
                              margin: 10,
                              height: 30,
                              width: 30,
                            }}
                          ></img>
                        </button>
                        <button className="green-button"
                          onClick={() => copyPrescription(item)}
                          style={{ marginRight: 0, display: item.status === 0 ? 'none' : 'flex' }}
                          title="COPIAR PRESCRIÇÃO."
                        >
                          <img
                            alt=""
                            src={copiar}
                            style={{
                              margin: 10,
                              height: 30,
                              width: 30,
                            }}
                          ></img>
                        </button>
                        <button className="green-button"
                          onClick={() => updatePrescription(item)}
                          style={{ marginRight: 0, display: item.status === 0 ? 'flex' : 'none' }}
                          title="SALVAR PRESCRIÇÃO."
                        >
                          <img
                            alt=""
                            src={salvar}
                            style={{
                              margin: 10,
                              height: 30,
                              width: 30,
                            }}
                          ></img>
                        </button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div >
      );
    } else {
      return null;
    }
  }
  // função que seleciona uma prescrição.
  const selectPrescricao = (item) => {
    setidprescricao(item.id);
    setstatusprescricao(item.status);
    loadItensPrescricoes(item.id, filteritemprescricao);
  }
  // carregando os itens de uma prescrição selecionada.
  const [listitensprescricao, setlistitensprescricoes] = useState([]);
  const loadItensPrescricoes = (item, filteritemprescricao) => {
    axios.get(html + "/itensprescricao/'" + item + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      setlistitensprescricoes(response.data);
      document.getElementById('LISTA DE ITENS PRESCRITOS').scrollTop = scrollposition;
      setarrayitemprescricao(x.filter(item => item.farmaco.includes(filteritemprescricao) === true));
      document.getElementById('LISTA DE ITENS PRESCRITOS').scrollTop = scrollposition;
      if (filteritemprescricao === '') {
        setarrayitemprescricao(x);
        document.getElementById('LISTA DE ITENS PRESCRITOS').scrollTop = scrollposition;
        setarrayoptionsitens([]);
        document.getElementById('LISTA DE ITENS PRESCRITOS').scrollTop = scrollposition;
      } else {
        setarrayoptionsitens(optionsitens.filter(item => item.farmaco.includes(filteritemprescricao) === true));
        document.getElementById('LISTA DE ITENS PRESCRITOS').scrollTop = scrollposition;
      }
      loadComponents();
      document.getElementById('LISTA DE ITENS PRESCRITOS').scrollTop = scrollposition;
    });
  }
  // seleção do item de prescrição.
  const [expanditem, setexpanditem] = useState(0);
  const selectItem = (item) => {
    setiditem(item.id);
    if (expanditem === 0 && iditem === item.id) {
      setexpanditem(1);
    } else {
      setexpanditem(0);
    }
  }
  // deletando item da prescrição.
  const deleteItem = (item) => {
    axios.get(html + "/deleteitemprescricao/'" + item.id + "'");
    setTimeout(() => {
      loadItensPrescricoes(idprescricao, filteritemprescricao);
    }, 1000);
    // EXCLUINDO TODOS OS REGISTROS DE COMPONENTES PARA ESTE ITEM.
    axios.get(html + "/deleteitemcomponentesprescricao/" + item.id)
    // EXCLUINDO TODOS OS REGISTROS DE APRAZAMENTO PARA ESTE ITEM.
    axios.get(html + "/deletechecagemprescricao/" + item.id)
  }
  // suspendendo item da prescrição.
  const suspendItem = (item) => {
    var obj = {
      idprescricao: idprescricao,
      codigo: item.codigo,
      grupo: item.grupo,
      farmaco: item.farmaco,
      qtde: item.qtde,
      via: item.via,
      horario: item.horario,
      observacao: item.observacao,
      status: 1,
    };
    axios.post(html + "/updateitemprescricao/" + item.id, obj);
    setTimeout(() => {
      setexpanditem(0);
      loadItensPrescricoes(idprescricao, filteritemprescricao);
    }, 1000);
  }
  // inserindo item na prescrição.
