import React, { useState } from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import { Doughnut } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import logo from '../images/logo.png';
import moment, { locale } from 'moment';

function Pacientes() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';

  const changeState = useDispatch();
  const viewPacientes = useSelector((state) => state.pacientes);
  const [cleanrender, setcleanrender] = useState(0);

  // recuperando estados atualizados.
  const idusuario = useSelector((state) => state.idusuario);
  const usuario = useSelector((state) => state.nomeusuario);
  const funcao = useSelector((state) => state.funcao);
  const tipo = useSelector((state) => state.tipo);
  const hospital = useSelector((state) => state.nomehospital);
  const unidade = useSelector((state) => state.nomeunidade);

  // carregamento do número de leitos do cti selecionado.
  const [leitos, setLeitos] = useState(10);
  const loadLeitos = () => {
    // ROTA: SELECT * FROM hospitaisxunidades WHERE hospital = hospital AND unidade = unidade.
    axios.get(html + "/totalleitosunidade/'" + hospital + "'/'" + unidade + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      const arrayLeitos = x.map((item) => item.leitos);
      setLeitos(arrayLeitos[0]);
    });
  }

  // carregamento da lista de pacientes para a unidade selecionada, conforme o status.
  const [pacientes, setPacientes] = useState([]);
  const [pacientesred, setPacientesred] = useState([]);
  const [pacientesyellow, setPacientesyellow] = useState([]);
  const [pacientesgreen, setPacientesgreen] = useState([]);
  const loadPacientes = () => {
    // todos os pacientes.
    // ROTA: SELECT * FROM atendimentos WHERE hospital = hospital AND unidade = unidade.
    axios.get(html + "/pacientesall/'" + hospital + "'/'" + unidade + "'").then((response) => {
      setPacientes(response.data);
    });
    // pacientes classificados como INSTÁVEIS.
    // ROTA: SELECT * FROM atendimentos WHERE hospital = hospital AND unidade = unidade AND status = 1.
    axios.get(html + "/pacientesred/'" + hospital + "'/'" + unidade + "'").then((response) => {
      setPacientesred(response.data);
    });
    // pacientes classificados como em ALERTA.
    // ROTA: SELECT * FROM atendimentos WHERE hospital = hospital AND unidade = unidade AND status = 2.
    axios.get(html + "/pacientesyellow/'" + hospital + "'/'" + unidade + "'").then((response) => {
      setPacientesyellow(response.data);
    });
    // pacientes classificados como ESTÁVEIS.
    // ROTA: SELECT * FROM atendimentos WHERE hospital = hospital AND unidade = unidade AND status = 3.
    axios.get(html + "/pacientesgreen/'" + hospital + "'/'" + unidade + "'").then((response) => {
      setPacientesgreen(response.data);
    });
  }

  // renderização da lista de pacientes.
  function ShowPacientes() {
    if (pacientes.length > 0) {
      return (
        <div
          className="scroll"
          id="LISTA DE PACIENTES"
          style={{
            display: 'flex',
            flex: 9,
            justifyContent: 'flex-start',
            borderRadius: 5,
            margin: 5,
          }}
        >
          {pacientes.map((item) => (
            <p
              onClick={() => selectPaciente(item)}
              key={item.id}
              className="row"
              style={{
                position: 'relative',
                margin: 0,
              }}
            >
              <button
                className="widget"
                style={{ minWidth: 50, margin: 2.5, color: '#ffffff' }}
                title="BOX"
                disabled="true"
              >
                {item.box}
              </button>
              <button
                className="status"
                data-status={item.status}
                title={
                  'STATUS: ' +
                  item.status +
                  '. CLIQUE PARA EVOLUIR. \nINTERCONSULTAS: ' +
                  item.inter
                }
                style={{
                  margin: 2.5,
                  width: '70%',
                }}
              >
                {item.nome}
              </button>
              <button
                className="status"
                data-status={item.status}
                title={
                  'STATUS: ' +
                  item.status +
                  '. CLIQUE PARA EVOLUIR. \nINTERCONSULTAS: ' +
                  item.inter
                }
                style={{
                  margin: 2.5,
                  width: '30%',
                }}
              >
                {moment().diff(moment(item.admissao, 'DD/MM/YY'), 'days') + ' DIAS.'}
              </button>
            </p>
          ))}
        </div>
      );
    } else {
      return (
        <div
          className="scroll"
          id="LISTA DE PACIENTES"
          style={{
            display: 'flex',
            flex: 5,
            justifyContent: 'flex-start',
            borderRadius: 5,
            margin: 5,
            marginLeft: 0,
            height: '70vh',
          }}
        >
          <p className="title2"
            style={{
              fontSize: 22,
              fontWeight: 'bold',
              margin: 10,
              padding: 0,
            }}>
            {'NÃO HÁ PACIENTES INTERNADOS NESTA UNIDADE.'}
          </p>
        </div>
      )
    }
  }

  function ShowPacientesHeader() {
    return (
      <div
        className="row"
        style={{ width: '100%', paddingLeft: 10, paddingRight: 35 }}
      >
        <button
          className="header"
          style={{
            width: 50,
            margin: 2.5,
          }}
          title="BOX"
          disabled="true"
        >
          BOX
          </button>
        <button
          className="header"
          style={{
            margin: 2.5,
            width: '70%',
          }}
        >
          NOME
          </button>
        <button
          className="header"
          style={{
            margin: 2.5,
            width: '30%',
          }}
        >
          TEMPO DE INTERNAÇÃO
          </button>
      </div >
    );
  }

  // CHART.
  /* gráfico em torta que exibe o total de pacientes internados na unidade, distribuídos
  por gravidade. */
  const dataChart = {
    labels: [' INSTÁVEIS', ' EM ALERTA', ' ESTÁVEIS', 'VAGOS'],
    datasets: [
      {
        data: [
          pacientesred.length,
          pacientesyellow.length,
          pacientesgreen.length,
          leitos -
          (pacientesred.length +
            pacientesyellow.length +
            pacientesgreen.length),
        ],
        backgroundColor: [
          '#F1948A',
          '#F7DC6F',
          '#7DCEA0',
          'rgba(153,163,164,1)',
        ],
        borderColor: '#F1F3F9',
        hoverBorderColor: ['#F1F3F9', '#F1F3F9', '#F1F3F9', '#F1F3F9'],
      },
    ],
  };
  function Chart() {
    if (pacientes.length > 0) {
      return (
        <div>
          <div
            className="secondary"
            style={{
              display: 'flex',
              flex: 3,
              flexDirection: 'column',
              justifyContent: 'space-around',
              alignItems: 'center',
              backgroundColor: '#f1f3f9',
              borderColor: '#f1f3f9',
              borderRadius: 5,
              padding: 10,
              paddingTop: 30,
              margin: 5,
              height: '70vh',
            }}
          >
            <div id="chart">
              <Doughnut
                data={dataChart}
                width={300}
                height={300}
                plugins={ChartDataLabels}
                options={{
                  plugins: {
                    datalabels: {
                      display: function (context) {
                        return context.dataset.data[context.dataIndex] !== 0;
                      },
                      color: '#FFFFFF',
                      font: {
                        weight: 'bold',
                        size: 16,
                      },
                    },
                  },
                  tooltips: {
                    enabled: false,
                  },
                  hover: { mode: null },
                  elements: {
                    arc: {
                      borderColor: '#F1F3F9',
                      borderWidth: 5,
                    },
                  },
                  animation: {
                    duration: 0,
                  },
                  title: {
                    display: false,
                    text: 'STATUS DOS PACIENTES:',
                  },
                  legend: {
                    display: false,
                    position: 'bottom',
                  },
                  maintainAspectRatio: true,
                  responsive: false,
                }}
              ></Doughnut>
            </div>
            <div>
              <p
                className="title2"
                style={{
                  fontSize: 16,
                  fontWeight: 'bold',
                  margin: 2.5,
                  marginTop: 10,
                  padding: 0,
                }}
              >
                {'OCUPAÇÃO: ' + Math.ceil(pacientes.length * 100 / leitos) + '%'}
              </p>
            </div>
            <Legenda></Legenda>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // exibição do percentual de ocupação do setor.
  function Ocupacao() {
    if (preparedchart === 1) {
      return (
        <div
          className="secondary"
          style={{
            position: 'absolute',
            width: 300,
            height: 300,
            top: document.getElementById("chart").getBoundingClientRect().top,
            bottom: document.getElementById("chart").getBoundingClientRect().bottom,
            left: document.getElementById("chart").getBoundingClientRect().left,
            right: document.getElementById("chart").getBoundingClientRect().right,
            flexDirection: 'column',
          }}
        >
          <p className="title2" style={{ fontSize: 14, margin: 0 }}>{'OCUPAÇÃO:'}</p>
          <p className="title2" style={{ fontSize: 18, margin: 0 }}>{pacientes.length * 100 / leitos + '%'}</p>
        </div>
      );
    } else {
      return null;
    }
  }

  // legenda para o gráfico.
  function Legenda() {
    return (
      <div
        className="secondary"
        style={{ flexDirection: 'row', justifyContent: 'center', marginTop: 20 }}
      >
        <div
          id="ESTÁVEIS"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: '#52be80',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          ESTÁVEIS
        </p>
        <div
          id="EM ALERTA"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: '#f7dc6f',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          EM ALERTA
        </p>
        <div
          id="INSTÁVEIS"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: '#ec7063',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          INSTÁVEIS
        </p>
        <div
          id="VAGOS"
          style={{
            width: 20,
            height: 20,
            borderRadius: 5,
            backgroundColor: 'rgba(153,163,164,1)',
            margin: 2.5,
            padding: 0,
          }}
        ></div>
        <p
          className="title2"
          style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
        >
          VAGOS
        </p>
      </div>
    );
  }

  // selecionando um paciente da lista e abrindo a tela corrida.
  const selectPaciente = (item) => {
    changeState({
      type: 'CORRIDA_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadFuncao: funcao,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdatendimento: item.id,
    });
  };

  const [preparedchart, setpreparedchart] = useState(0);
  useEffect(() => {
    if (viewPacientes === 1) {
      setcleanrender(0);
      // carregndo o total de leitos da unidade.
      loadLeitos();
      // carregando a lista de pacientes.
      loadPacientes();
      // renderizando a tela após a construção "tempestuosa" dos gráficos.
      setTimeout(() => {
        setcleanrender(1);
      }, 1000);
      setTimeout(() => {
        setpreparedchart(1);
      }, 3000);
    }
  }, [viewPacientes]);

  // HEADER.
  const clickLogin = () => {
    changeState({ type: 'LOGIN_ON' });
  };
  // função para retornar à tela Login.
  const clickVoltar = () => {
    changeState({
      type: 'UNIDADE_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
    });
  };
  // função para acessar a tela Password.
  const clickPassword = () => {
    changeState({
      type: 'PASSWORD_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
    });
  };
  // function Header.
  function Header() {
    return (
      <div
        id="HEADER"
        style={{
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'center',
          alignItems: 'center',
          margin: 5,
          padding: 5,
          width: '100%',
          maxHeight: '20vh',
        }}
      >
        <div
          className="secondary"
          style={{
            display: 'flex',
            flex: 1,
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <img
            alt=""
            src={logo}
            style={{
              marginTop: 15,
              marginBottom: 15,
              marginLeft: -5,
              marginRight: -5,
              height: '90%',
            }}
          ></img>
        </div>
        <div
          className="secondary"
          style={{
            display: 'flex',
            flex: 7,
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <div style={{ display: 'flex', flexDirection: 'row' }}>
            <p className="title1" style={{ color: '#1F7A8C' }}>
              {'LISTA DE PACIENTES: '}
            </p>
            <p className="title1" style={{ color: '#ec7063' }}>
              {hospital + ' - ' + unidade}
            </p>
          </div>
        </div>
        <div
          className="secondary"
          style={{
            display: 'flex',
            flexDirection: 'column',
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <button
            className="blue-button"
            title="CLIQUE PARA ALTERAR SUA SENHA."
            onClick={() => clickPassword()}
            style={{
              display: 'flex',
              width: 200,
              margin: 10,
              marginBottom: 0,
              paddingLeft: 20,
              paddingRight: 20,
            }}
          >
            OLÁ, {usuario}.
          </button>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
            <button
              className="blue-button"
              title="VOLTAR PARA A LISTA DE HOSPTAIS."
              onClick={() => clickVoltar()}
              style={{
                display: 'flex',
                width: 97.5,
                margin: 10,
                marginTop: 5,
                marginRight: 0,
                paddingLeft: 20,
                paddingRight: 20,
              }}
            >
              VOLTAR
            </button>
            <button
              className="blue-button"
              title="FAZER LOGOFF."
              onClick={() => clickLogin()}
              style={{
                display: 'flex',
                width: 97.5,
                margin: 10,
                marginTop: 5,
                marginLeft: 5,
                paddingLeft: 20,
                paddingRight: 20,
              }}
            >
              SAIR
            </button>
          </div>
        </div>
      </div>
    );
  }

  // renderização do componente.
  if (viewPacientes === 1 && cleanrender === 1) {
    return (
      <div
        className="main fade-in"
        style={{
          display: 'flex',
          width: '100%',
          flexDirection: 'column',
        }}
      >
        <Header></Header>
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            width: '100%',
            paddingLeft: 5,
            paddingRight: 5,
          }}>
          <Chart></Chart>
          <div
            style={{
              display: 'flex',
              flex: 10,
              flexDirection: 'column',
              width: '100%',
            }}>
            <ShowPacientesHeader></ShowPacientesHeader>
            <ShowPacientes></ShowPacientes>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Pacientes;
