import React, { useState } from 'react';
import { useEffect } from 'react';
import moment from 'moment';
import { useSelector, useDispatch } from 'react-redux';
import deletar from '../images/deletar.svg';
import salvar from '../images/salvar.svg';

function DatePicker(viewdatepicker) {

  var month = moment().format('MM');
  var year = moment().format('YY');
  const [selecteddata, setselecteddata] = useState(moment().format('DD/MM'));
  var firstdate = moment('01/' + month + '/' + year).format('DD/MM/YYYY');
  var startdate = moment(firstdate);
  var enddate = moment(startdate).add(42, 'days');
  var arraydate = [];

  useEffect(() => {
    if (viewdatepicker === 1){
      // ???
    }
  }, [viewdatepicker])

  // descobrindo o primeiro dia do calendário (último domingo do mês anteior).
  const firstSunday = () => {
    while (startdate.weekday() > 0) {
      startdate = startdate.subtract(1, 'day');
      enddate = enddate.subtract(1, 'day');
    }
    // se o primeiro domingo da array ainda cair no mês atual:
    if (startdate.month() === moment().month()) {
      startdate = startdate.subtract(7, 'days');
      enddate = enddate.subtract(7, 'days');
    }
  }
  // criando array com 42 dias a partir da startdate.
  const setArrayDate = () => {
    arraydate = [startdate.format('DD/MM/YY')];
    console.log('START: ' + startdate.format('DD/MM/YY') + '; FINAL: ' + enddate.format('DD/MM/YY') + '.')
    while (enddate.diff(startdate, 'days') > 1) {
      startdate = startdate.add(1, 'day');
      arraydate.push(startdate.format('DD/MM/YY').toString());
    }
  }

  // selecionando uma data no datepicker.
  const selectDate = (value) => {
    setselecteddata(value);
  }

  // renderização do componente.
  if (viewdatepicker === 1) {
    firstSunday();
    setArrayDate();
    console.log(arraydate);
    return (
      <div
        className="secondary"
        id="LISTA DE DATAS"
        style={{
          position: 'absolute',
          display: 'flex',
          flexDirection: 'row',
          flexWrap: 'wrap',
          justifyContent: 'center',
          alignItems: 'center',
          borderRadius: 5,
          margin: 0,
          padding: 5,
          width: 395,
          height: 340,
          backgroundColor: '#ffffff',
          zIndex: 5,
        }}
      >
        {arraydate.map((item) => (

          <button
            className="widget"
            onClick={() => selectDate(item)}
            style={{
              width: 50,
              height: 50,
              margin: 2.5,
              color: '#ffffff',
              backgroundColor: item === selecteddata ? '#ec7063' : '#5dade2',
            }}
            title={item}
          >
            {item.substring(0, 2)}
          </button>
        ))}
      </div>
    );
  } else {
    return null;
  }
}
export default DatePicker;
