import React, { useState } from 'react';
import axios from 'axios';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import deletar from '../images/deletar.svg';
import salvar from '../images/salvar.svg';
import moment, { locale } from 'moment';

function UpdateAtendimento() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';

  const changeState = useDispatch();
  const viewUpdateAtendimento = useSelector((state) => state.updateatendimento);

  // recuperando estados atualizados.
  const idusuario = useSelector((state) => state.idusuario);
  const usuario = useSelector((state) => state.nomeusuario);
  const tipo = useSelector((state) => state.tipo);
  const hospital = useSelector((state) => state.nomehospital);
  const unidade = useSelector((state) => state.nomeunidade);
  const idatendimento = useSelector((state) => state.idatendimento);

  // estados relacionados ao atendimento.
  const [idpaciente, setidpaciente] = useState(0);
  const [box, setbox] = useState('');
  const [admissao, setadmissao] = useState('');
  const [dn, setdn] = useState('');
  const [nome, setnome] = useState('');
  const [peso, setpeso] = useState('');
  const [altura, setaltura] = useState('');
  const [antecedentes, setantecedentes] = useState('');
  const [alergias, setalergias] = useState('');
  const [medicacoes, setmedicacoes] = useState('');
  const [exames, setexames] = useState('');
  const [historia, sethistoria] = useState('');
  const [status, setstatus] = useState(0);
  // carregando o atendimento do paciente selecionado.
  const loadAtendimento = () => {
    axios.get(html + "/atendimento/'" + hospital + "'/'"
      + unidade + "'/'"
      + idatendimento + "'").then((response) => {
        var x = [0, 1];
        x = response.data;
        setidpaciente(x.map((item) => item.idpaciente));
        setbox(x.map((item) => item.box));
        setadmissao(x.map((item) => item.admissao));
        setdn(x.map((item) => item.dn));
        setnome(x.map((item) => item.nome));
        setpeso(x.map((item) => item.peso));
        setaltura(x.map((item) => item.altura));
        setantecedentes(x.map((item) => item.antecedentes));
        setalergias(x.map((item) => item.alergias));
        setmedicacoes(x.map((item) => item.medicacoes));
        setexames(x.map((item) => item.exames));
        sethistoria(x.map((item) => item.historia));
        setstatus(x.map((item) => item.status));
      });
  }

  // preenchendo os campos.
  useEffect(() => {
    loadAtendimento();
  }, [viewUpdateAtendimento]);

  // função que atualiza o paciente.
  const updateAtendimento = () => {
    var obj = {
      idpaciente: idpaciente,
      hospital: hospital,
      unidade: unidade,
      box: box,
      admissao: admissao,
      nome: nome,
      dn: dn,
      peso: peso,
      altura: altura,
      antecedentes: document.getElementById('inputAntecedentes').value.toUpperCase(),
      alergias: document.getElementById('inputAle').value.toUpperCase(),
      medicacoes: document.getElementById('inputMedicacoes').value.toUpperCase(),
      exames: document.getElementById('inputExames').value.toUpperCase(),
      historia: document.getElementById('inputHistoria').value.toUpperCase(),
      status: status,
      ativo: 1,
    };
    axios.post(html + '/updateatendimento/' + idatendimento, obj);
    setTimeout(() => {
      close();
    }, 1000);
  };

  const close = () => {
    changeState({
      type: 'CORRIDA_OFF',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdatendimento: idatendimento,
    });
    changeState({
      type: 'CORRIDA_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdatendimento: idatendimento,
    });
  };

  // varíavel 'status'.
  const clickStatus1 = () => {
    setstatus(1);
  };
  const clickStatus2 = () => {
    setstatus(2);
  };
  const clickStatus3 = () => {
    setstatus(3);
  };

  // renderização do componente.
  if (viewUpdateAtendimento === 1) {
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          height: '100%',
          width: '100%',
          marginTop: 0,
          marginBottom: 0,
          marginLeft: 0,
          marginRight: 0,
        }}
      >
        <div>
          <div
            className="secondary"
            style={{
              backgroundColor: '#FFFFFF',
              borderRadius: 5,
              paddingTop: 30,
              paddingBottom: 30,
              paddingLeft: 30,
              paddingRight: 30,
            }}
          >
            <label
              className="title2"
              style={{ marginTop: 0, marginBottom: 15 }}
            >
              ATUALIZAR ATENDIMENTO
            </label>
            <div
              className="scroll"
              style={{
                justifyContent: 'flex-start',
                height: 400,
                margin: 5,
                paddingRight: 5,
              }}
            >
              <div
                id="IDENTIFICAÇÃO DO PACIENTE."
                title="APENAS A SECRETÁRIA PODE MUDAR ESTES DADOS."
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  margin: 2.5,
                }}
              >
                <div className="widget" style={{ margin: 2.5, padding: 5, fontSize: 14, width: 120, height: 60, backgroundColor: '#279AB1' }}>{'BOX ' + box}</div>
                <div className="widget" style={{ margin: 2.5, padding: 5, fontSize: 14, width: 300, height: 60, backgroundColor: '#279AB1' }}>{nome}</div>
                <div className="widget" style={{ margin: 2.5, padding: 5, fontSize: 14, width: 120, height: 60, backgroundColor: '#279AB1' }}>{(moment().diff(moment(dn, 'DD/MM/YYYY'), 'years') + ' ANOS')}</div>
              </div>
              <label
                className="title2"
                style={{
                  marginTop: 15,
                  fontSize: 14,
                  justifyContent: 'center',
                }}
              >
                STATUS:
                  </label>
              <div
                id="STATUS DO PACIENTE."
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  margin: 5,
                }}
              >
                <div
                  class="blue-button"
                  onClick={() => clickStatus3()}
                  style={{
                    display: 'flex',
                    flexDirection: 'row',
                    padding: 10,
                    width: 130,
                    marginTop: 0,
                    marginBottom: 0,
                    backgroundColor: status == 3 ? '#52be80' : '#1f7a8c',
                  }}
                >
                  ESTÁVEL
              </div>
                <div
                  class="blue-button"
                  onClick={() => clickStatus2()}
                  style={{
                    display: 'flex',
                    flexDirection: 'row',
                    padding: 10,
                    width: 130,
                    marginTop: 0,
                    marginBottom: 0,
                    backgroundColor: status == 2 ? '#f7dc6f' : '#1f7a8c',
                  }}
                >
                  ALERTA
              </div>
                <div
                  class="blue-button"
                  onClick={() => clickStatus1()}
                  style={{
                    display: 'flex',
                    flexDirection: 'row',
                    padding: 10,
                    width: 130,
                    marginTop: 0,
                    marginBottom: 0,
                    backgroundColor: status == 1 ? '#ec7063' : '#1f7a8c',
                  }}
                >
                  INSTÁVEL
              </div>
              </div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  marginTop: 0,
                }}
              >
                <div>
                  <label
                    className="title2"
                    style={{
                      marginTop: 15,
                      fontSize: 14,
                      justifyContent: 'left',
                      paddingLeft: 15,
                    }}
                  >
                    ANTECEDENTES PESSOAIS:
                  </label>
                  <textarea
                    className="textarea"
                    title="ANTECEDENTES PESSOAIS."
                    id="inputAntecedentes"
                    type="text"
                    defaultValue={antecedentes}
                    maxLength="200"
                    autoComplete="off"
                    placeholder="ANTECEDENTES PESSOAIS"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) =>
                      (e.target.placeholder = 'ANTECEDENTES PESSOAIS')
                    }
                    style={{
                      resize: 'none',
                      marginBottom: 0,
                      marginRight: 0,
                      width: 350,
                      height: 90,
                    }}
                  ></textarea>
                </div>
                <div>
                  <label
                    className="title2"
                    style={{
                      marginTop: 15,
                      fontSize: 14,
                      justifyContent: 'left',
                      paddingLeft: 15,
                    }}
                  >
                    ALERGIAS:
                  </label>
                  <textarea
                    className="textarea"
                    title="ALERGIAS."
                    id="inputAle"
                    type="text"
                    defaultValue={alergias}
                    maxLength="200"
                    autoComplete="off"
                    placeholder="ALERGIAS"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) =>
                      (e.target.placeholder = 'ALERGIAS')
                    }
                    style={{
                      resize: 'none',
                      marginBottom: 0,
                      marginRight: 0,
                      width: 350,
                      height: 90,
                    }}
                  ></textarea>
                </div>
              </div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                }}
              >
                <div>
                  <label
                    className="title2"
                    style={{
                      marginTop: 15,
                      fontSize: 14,
                      justifyContent: 'left',
                      paddingLeft: 15,
                    }}
                  >
                    MEDICAÇÕES PRÉVIAS:
                  </label>
                  <textarea
                    className="textarea"
                    title="MEDICAÇÕES PRÉVIAS."
                    id="inputMedicacoes"
                    type="text"
                    defaultValue={medicacoes}
                    maxLength="200"
                    autoComplete="off"
                    placeholder="MEDICAÇÕES PRÉVIAS"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) =>
                      (e.target.placeholder = 'MEDICAÇÕES PRÉVIAS')
                    }
                    style={{
                      resize: 'none',
                      marginBottom: 0,
                      marginRight: 0,
                      width: 350,
                      height: 90,
                    }}
                  ></textarea>
                </div>
                <div>
                  <label
                    className="title2"
                    style={{
                      marginTop: 15,
                      fontSize: 14,
                      justifyContent: 'left',
                      paddingLeft: 15,
                    }}
                  >
                    EXAMES PRÉVIOS:
                  </label>
                  <textarea
                    className="textarea"
                    title="EXAMES PRÉVIOS."
                    id="inputExames"
                    type="text"
                    defaultValue={exames}
                    maxLength="200"
                    autoComplete="off"
                    placeholder="EXAMES PRÉVIOS"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) => (e.target.placeholder = 'EXAMES PRÉVIOS')}
                    style={{
                      resize: 'none',
                      marginBottom: 0,
                      width: 350,
                      height: 90,
                    }}
                  ></textarea>
                </div>
              </div>
              <div>
                <label
                  className="title2"
                  style={{
                    marginTop: 15,
                    fontSize: 14,
                    justifyContent: 'left',
                    paddingLeft: 15,
                  }}
                >
                  HISTÓRIA DA DOENÇA ATUAL:
                  </label>
                <textarea
                  className="textarea"
                  title="HISTÓRIA DA DOENÇA ATUAL."
                  id="inputHistoria"
                  type="text"
                  defaultValue={historia}
                  maxLength="200"
                  autoComplete="off"
                  placeholder="HISTÓRIA DA DOENÇA ATUAL"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) =>
                    (e.target.placeholder = 'HISTÓRIA DA DOENÇA ATUAL')
                  }
                  style={{
                    resize: 'none',
                    marginBottom: 5,
                    width: 705,
                    height: 90,
                  }}
                ></textarea>
              </div>
            </div>
            <div
              style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'center',
                marginTop: 25,
              }}
            >
              <button
                className="red-button"
                onClick={() => close()}
              >
                <img
                  alt=""
                  src={deletar}
                  style={{
                    marginTop: 20,
                    marginBottom: 20,
                    marginLeft: 20,
                    marginRight: 20,
                    height: 20,
                    width: 20,
                  }}
                ></img>
              </button>
              <button
                className="green-button"
                onClick={() => updateAtendimento()}
                style={{
                  marginRight: 0,
                }}
              >
                <img
                  alt=""
                  src={salvar}
                  style={{
                    marginTop: 20,
                    marginBottom: 20,
                    marginLeft: 20,
                    marginRight: 20,
                    height: 20,
                    width: 20,
                  }}
                ></img>
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default UpdateAtendimento;
