import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import deletar from '../images/deletar.svg';
import salvar from '../images/salvar.svg';
import Toast from './Toast';
import MaskedInput from 'react-maskedinput';

function Imagem(
  { viewimagem,
    idAtendimento,
  }) {
  //servidor.
  var html = 'https://pulsarapp-server.herokuapp.com';
  const changeState = useDispatch();

  // chave para exibição do componente.
  const [viewcomponent, setviewcomponent] = useState(viewimagem);

  useEffect(() => {
    if (viewimagem === 1) {
      setviewcomponent(viewimagem);
      // limpando as listas de exames disponíveis e filtrados.
      setselectedlistimagem([]);
      setarrayfilterimagem([]);
      setfilterimagem('');
    } else {
    }
  }, [viewimagem])

  // inserindo registro.
  const insertData = (item) => {
    var obj = {
      idatendimento: item.idatendimento,
      codigo: item.codigo,
      exame: item.exame,
      laudo: item.laudo,
      justificativa: justify.toUpperCase(),
      status: item.status,
      pedido: moment().format('DD/MM/YY HH:mm'),
      resultado: item.resultado,
    };
    axios.post(html + '/insertimage', obj);
  };

  const [arrayimagem, setarrayimagem] = useState([]);
  // inserindo os registros de exames de imagem solicitados no banco de dados.
  const insertImagem = () => {
    arrayimagem.map((item) => insertData(item));
    setTimeout(() => {
      setviewcomponent(0);
    }, 1000);
  }

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time + 1000);
  }


  // PENDÊNCIA!
  // lista de exames de imagem disponíveis para seleção.
  const [listimagem, setlistimagem] = useState([
    {
      idatendimento: idAtendimento,
      codigo: 11,
      exame: 'TC DE CRÂNIO',
      laudo: '',
      status: 'PENDENTE',
      datapedido: '',
      dataresultado: '',
    }
  ]);
  // array com os exames de imagem selecionados.
  const [selectedlistimagem, setselectedlistimagem] = useState([]);
  // funções e componentes para busca e adição de outros exames.
  const [filterimagem, setfilterimagem] = useState('');
  var searchimagem = '';
  var timeout = null;
  const [arrayfilterimagem, setarrayfilterimagem] = useState([listimagem]);
  const filterImagem = () => {
    clearTimeout(timeout);
    document.getElementById("inputFilterImagem").focus();
    searchimagem = document.getElementById("inputFilterImagem").value.toUpperCase();
    timeout = setTimeout(() => {
      if (searchimagem === '') {
        setarrayfilterimagem([]);
        document.getElementById("inputFilterImagem").value = '';
        document.getElementById("inputFilterImagem").focus();
      } else {
        setfilterimagem(document.getElementById("inputFilterImagem").value.toUpperCase());
        setarrayfilterimagem(listimagem.filter(item => item.exame.includes(searchimagem) === true));
        document.getElementById("inputFilterImagem").value = searchimagem;
        document.getElementById("inputFilterImagem").focus();
      }
    }, 3000);
  }
  const addImagem = (item) => {
    var exame = item.exame;
    var newimagem = {
      idatendimento: item.idatendimento,
      codigo: item.codigo,
      exame: item.exame,
      laudo: item.laudo,
      status: item.status,
      pedido: item.pedido,
      resultado: item.resultado,
    }
    const x = arrayimagem.indexOf((item) => item.exame === exame);
    if (x !== '') {
      arrayimagem.push(newimagem);
      selectedlistimagem.push(newimagem);
      setarrayfilterimagem([]);
      setfilterimagem('');
      document.getElementById("inputFilterImagem").value = '';
      document.getElementById("inputFilterImagem").focus();
    }
  }
  const deleteImagem = (item) => {
    var exame = item.exame;
    const x = arrayimagem.indexOf((item) => item.exame === exame);
    const y = selectedlistimagem.indexOf((item) => item.exame === exame);
    arrayimagem.splice(x, 1);
    selectedlistimagem.splice(y, 1);
    setarrayfilterimagem([]);
    setfilterimagem('');
    document.getElementById("inputFilterImagem").value = '';
    document.getElementById("inputFilterImagem").focus();
  }

  var justify = '';
  const setJustificativa = () => {
    justify = document.getElementById("inputJustificativa").value;
  }

  function ShowSearchImagem() {
    return (
      <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', marginLeft: 5 }}>
        <input
          className="input"
          autoComplete="off"
          placeholder="BUSCAR EXAME..."
          onFocus={(e) => (e.target.placeholder = '')}
          onBlur={(e) => (e.target.placeholder = 'BUSCAR EXAME...')}
          onChange={() => filterImagem()}
          style={{
            width: 0.3 * window.innerWidth,
            margin: 5,
            marginRight: 0,
          }}
          type="text"
          id="inputFilterImagem"
          defaultValue={filterimagem}
          maxLength={100}
        ></input>
        <div
          className="scroll"
          id="LISTA DE EXAMES DE IMAGEM DISPONÍVEIS"
          style={{
            display: arrayfilterimagem.length > 0 ? 'flex' : 'none',
            justifyContent: 'flex-start',
            margin: 5,
            marginTop: 5,
            padding: 0,
            paddingRight: 5,
            height: 125,
            minWidth: 0.3 * (window.innerWidth),
            maxWidth: 0.3 * (window.innerWidth),
          }}
        >
          {arrayfilterimagem.map((item) => (
            <p
              key={item.id}
              id="item da lista"
              className="row"
              style={{ margin: 5, marginRight: 0, marginTop: 2.5, marginBottom: 2.5 }}
            >
              <button
                onClick={() => addImagem(item)}
                className="blue-button"
                style={{
                  width: '100%',
                  margin: 2.5,
                  flexDirection: 'column',
                }}
              >
                {item.exame}
              </button>
            </p>
          ))}
        </div>
        <div
          className="scroll"
          id="LISTA DE EXAMES DE IMAGEM SELECIONADOS"
          style={{
            display: arrayfilterimagem.length > 0 ? 'none' : 'flex',
            justifyContent: 'flex-start',
            margin: 5,
            marginTop: 5,
            padding: 0,
            paddingRight: 5,
            height: 125,
            minWidth: 0.3 * (window.innerWidth),
            maxWidth: 0.3 * (window.innerWidth),
          }}
        >
          {selectedlistimagem.map((item) => (
            <p
              key={item.id}
              id="item da lista"
              className="row"
              style={{ margin: 5, marginTop: 2.5, marginBottom: 2.5 }}
            >
              <button
                className="blue-button"
                style={{
                  width: '99%',
                  margin: 2.5,
                  flexDirection: 'column',
                }}
              >
                {item.exame}
              </button>
              <button className="red-button"
                onClick={() => deleteImagem(item)}
                style={{ marginRight: 0 }}
              >
                <img
                  alt=""
                  src={deletar}
                  style={{
                    margin: 10,
                    height: 30,
                    width: 30,
                  }}
                ></img>
              </button>
            </p>
          ))}
        </div>
        <textarea
          className="textarea"
          autoComplete="off"
          placeholder="JUSTIFICATIVA"
          onFocus={(e) => (e.target.placeholder = '')}
          onBlur={(e) => (e.target.placeholder = 'JUSTIFICATIVA')}
          onChange={() => setJustificativa()}
          style={{
            width: 0.3 * window.innerWidth,
            margin: 5,
            marginRight: 0,
          }}
          type="text"
          id="inputJustificativa"
          maxLength={300}
        ></textarea>
      </div>
    );
  }

  // renderização do componente.
  if (viewcomponent === 1) { // inserir.
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          margin: 0,
          zIndex: 5,
        }}
      >
        <div
          className="secondary"
          style={{
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            paddingTop: 30,
            paddingBottom: 30,
            paddingLeft: 30,
            paddingRight: 30,
          }}
        >
          <div className="title2" style={{ fontSize: 16 }}>SOLICITAR EXAME DE IMAGEM</div>
          <ShowSearchImagem></ShowSearchImagem>
          <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center' }}>
            <button
              className="green-button"
              onClick={() => insertImagem()}
              disabled={selectedlistimagem.length < 1 ? true : false}
              style={{
                width: 50,
                margin: 2.5,
                marginTop: 30,
                flexDirection: 'column',
                opacity: selectedlistimagem.length < 1 ? 0.5 : 1,
              }}
            >
              <img
                alt=""
                src={salvar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
            <button
              className="red-button"
              onClick={() => setviewcomponent(0)}
              style={{
                width: 50,
                margin: 2.5,
                marginTop: 30,
                flexDirection: 'column',
              }}
            >
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Imagem;