import React, { useState } from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import { Doughnut } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import logo from '../images/logo.png';

function Hospitais() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';

  const changeState = useDispatch();
  const viewHospitais = useSelector((state) => state.hospitais);
  const [cleanrender, setcleanrender] = useState(0);

  // recuperando estados atualizados.
  const loginid = useSelector((state) => state.idusuario);
  const loginnome = useSelector((state) => state.nomeusuario);
  const loginfuncao = useSelector((state) => state.funcao);
  const logintipo = useSelector((state) => state.tipo);

  // carregamento da lista de hospitais nos quais o usuário trabalha.
  const [hospitais, setHospitais] = useState([]);
  const loadHospitais = () => {
    // ROTA: SELECT * FROM usuarioxhospital WHERE idusuario = loginid.
    axios.get(html + "/hospitais/'" + loginid + "'").then((response) => {
      setHospitais(response.data);
    });
  }
  // montando a lista de hospitais.
  function ShowHospitais() {
    return (
      <div style={{
        display: 'flex',
        width: '100%',
        paddingLeft: 5,
        paddingRight: 5,
      }}>
        <div
          className="scroll"
          id="LISTA DE HOSPITAIS"
          style={{
            display: 'flex',
            width: '100%',
            height: '70vh',
            flexDirection: 'row',
            flexWrap: 'wrap',
            justifyContent: 'center',
            alignItems: 'center',
            borderRadius: 5,
            margin: 5,
          }}
        >
          {hospitais.map((item) => (
            GetData(item)
          ))}
        </div>
      </div>
    );
  }

  // selecionando um hosital da lista.
  const selectHospital = (item) => {
    changeState({
      type: 'UNIDADE_ON',
      // payloads.
      payloadIdusuario: loginid,
      payloadNomeusuario: loginnome,
      payloadFuncao: loginfuncao,
      payloadTipo: logintipo,
      payloadNomehospital: item.hospital,
    });
  }

  // selecionando a tela de atendimentos (apenas secretária).
  const selectAtendimento = (item) => {
    changeState({
      type: 'ATENDIMENTO_ON',
      // payloads.
      payloadIdusuario: loginid,
      payloadNomeusuario: loginnome,
      payloadTipo: logintipo,
    });
  }

  useEffect(() => {
    if (viewHospitais === 1) {
      setcleanrender(0);
      // carregando a lista de hospitais.
      loadHospitais()
      // renderizando a tela após a construção "tempestuosa" dos gráficos.
      setTimeout(() => {
        setcleanrender(1);
      }, 1000);
    }
  }, [viewHospitais]);

  // CHART.
  /* gráfico em torta que exibe o total de leitos vagos e o total
  de leitos ocupados para cada hospital. */
  var pacientes = 0;
  var leitos = 0;
  var dataChart = [];
  function GetData(item) {
    const [pct, setpct] = useState(0);
    const [lto, setlto] = useState(0);
    // ROTA: SELECT * FROM atendimentos WHERE hospital = hospital.
    axios.get(html + "/totalpacientes/'" + item.hospital + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      pacientes = x.length;
      setpct(pacientes);
    });
    // ROTA: SELECT * FROM hospitaisxunidades WHERE hospital = hospital.
    axios.get(html + "/totalleitos/'" + item.hospital + "'").then((response) => {
      var x = [0, 1];
      x = response.data;
      const arrayleitos = x.map((item) => item.leitos);
      // efetuando a soma de leitos.
      function somaLeitos(total, num) {
        return total + num;
      }
      leitos = arrayleitos.reduce(somaLeitos);
      setlto(leitos);
    });
    // gerando os dados do gráfico.
    console.log('LEITOS: ' + lto + ', PACIENTES: ' + pct + '.');
    dataChart = {
      labels: [' LIVRES', ' OCUPADOS'],
      datasets: [
        {
          data: [lto - pct, pct],
          backgroundColor: [
            '#52be80',
            '#ec7063',
          ],
          borderColor: '#E1E5F2',
          hoverBorderColor: ['#E1E5F2', '#E1E5F2'],
        },
      ],
    };
    return (
      <div>
        <div
          id="hospital"
          className="card"
          onClick={() => selectHospital(item)}
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            backgroundColor: '#E1E5F2',
            borderColor: '#E1E5F2',
            borderRadius: 5,
            padding: 10,
            margin: 5,
            height: 400,
          }}
        >
          <p className="title2"
            style={{
              fontSize: 22,
              fontWeight: 'bold',
              margin: 10,
              padding: 0,
            }}>
            {item.hospital}
          </p>
          <Doughnut
            data={dataChart}
            width={250}
            height={250}
            plugins={ChartDataLabels}
            options={{
              plugins: {
                datalabels: {
                  display: function (context) {
                    return context.dataset.data[context.dataIndex] !== 0;
                  },
                  color: '#FFFFFF',
                  font: {
                    weight: 'bold',
                    size: 16,
                  },
                },
              },
              tooltips: {
                enabled: false,
              },
              hover: { mode: null },
              elements: {
                arc: {
                  hoverBorderColor: '#E1E5F2',
                  borderColor: '#E1E5F2',
                  borderWidth: 5,
                },
              },
              animation: {
                duration: 0,
              },
              title: {
                display: false,
                text: 'OCUPAÇÃO DE LEITOS',
              },
              legend: {
                display: false,
                position: 'bottom',
              },
              maintainAspectRatio: true,
              responsive: false,
            }}
          ></Doughnut>
          <div style={{ flexDirection: 'column', justifyContent: 'center' }}>
            <div>
              <p
                className="title2"
                style={{
                  fontSize: 16,
                  fontWeight: 'bold',
                  margin: 2.5,
                  marginTop: 10,
                  padding: 0,
                }}
              >
                {'OCUPAÇÃO: ' + Math.ceil(pct * 100 / lto) + '%'}
              </p>
            </div>
            <div
              className="secondary"
              style={{
                flexDirection: 'row',
                marginTop: 5,
                marginBottom: 20,
              }}
            >
              <div
                id="VAGOS"
                style={{
                  width: 20,
                  height: 20,
                  borderRadius: 5,
                  backgroundColor: '#52be80',
                  margin: 2.5,
                  padding: 0,
                }}
              ></div>
              <p
                className="title2"
                style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
              >
                VAGOS
          </p>
              <div
                id="OCUPADOS"
                style={{
                  width: 20,
                  height: 20,
                  borderRadius: 5,
                  backgroundColor: '#ec7063',
                  margin: 2.5,
                  padding: 0,
                }}
              ></div>
              <p
                className="title2"
                style={{ fontSize: 14, margin: 2.5, marginRight: 5, padding: 0 }}
              >
                OCUPADOS
          </p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  // HEADER.
  const clickLogin = () => {
    if (logintipo === 3) {
      changeState({
        type: 'SECRETARIA_ON',
        // payloads.
        payloadIdusuario: loginid,
        payloadNomeusuario: loginnome,
        payloadTipo: logintipo,
      });
    } else {
      changeState({ type: 'LOGIN_ON' });
    }

  };
  // função para acessar a tela Password.
  const clickPassword = () => {
    changeState({
      type: 'PASSWORD_ON',
      // payloads.
      payloadIdusuario: loginid,
      payloadNomeusuario: loginnome,
      payloadTipo: logintipo,
    });
  };
  // function Header.
  function Header() {
    return (
      <div
        id="HEADER"
        style={{
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'center',
          alignItems: 'center',
          margin: 5,
          padding: 5,
          width: '100%',
          maxHeight: '20vh',
        }}
      >
        <div
          className="secondary"
          style={{
            display: 'flex',
            flex: 1,
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <img
            alt=""
            src={logo}
            style={{
              marginTop: 15,
              marginBottom: 15,
              marginLeft: -5,
              marginRight: -5,
              height: '90%',
            }}
          ></img>
        </div>
        <div
          className="secondary"
          style={{
            display: 'flex',
            flex: 7,
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <p className="title1" style={{ color: '#1F7A8C' }}>
            {'LISTA DE HOSPITAIS'}
          </p>
        </div>
        <div
          className="secondary"
          style={{
            display: 'flex',
            flexDirection: 'column',
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: '20vh',
          }}
        >
          <button
            className="blue-button"
            title="CLIQUE PARA ALTERAR SUA SENHA."
            onClick={() => clickPassword()}
            style={{
              display: 'flex',
              width: 200,
              margin: 10,
              marginBottom: 0,
              paddingLeft: 20,
              paddingRight: 20,
            }}
          >
            OLÁ, {loginnome}.
          </button>
          <button
            className="blue-button"
            title="FAZER LOGOFF."
            onClick={() => clickLogin()}
            style={{
              display: 'flex',
              width: 200,
              margin: 10,
              marginTop: 5,
              paddingLeft: 20,
              paddingRight: 20,
            }}
          >
            SAIR
            </button>
        </div>
      </div>
    );
  }

  // renderização do componente.
  if (viewHospitais === 1 && cleanrender === 1) {
    return (
      <div
        className="main fade-in"
        style={{
          display: 'flex',
          width: '100%',
          flexDirection: 'column',
        }}
      >
        <Header></Header>
        <ShowHospitais></ShowHospitais>
      </div>
    );
  } else {
    return null;
  }
}
export default Hospitais;
